<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">
        {{ $t("tools.info_text") }}
      </div>
      <div class="form_body">
        <div class="form_section">
          <div class="form_preview_heading">{{ $t("tools.preview") }}</div>
          <div class="form_preview_detail">
            <div v-if="title">
              {{ title }}
            </div>
            <div v-else>{{ $t("tools.label") }}</div>
            <b-form-textarea v-model="infoTextContent" :disabled="true"></b-form-textarea>
          </div>
        </div>
        <div class="form_section">
          <div class="form_section_heading">{{ $t("tools.label") }}</div>
          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.label_text") }}</label>
            <b-form-input v-model="title" :placeholder="$t('place_holders.info_text_label')" :class="{ 'input-error': textBoxTextError }"></b-form-input>
          </div>
          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.info_text_content") }}</label>
            <b-form-textarea v-model="infoTextContent" :placeholder="$t('place_holders.info_text')" :class="{ 'input-error': textAreaTextError }"></b-form-textarea>
          </div>
        </div>
      </div>
      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">{{ $t("buttons.cancel") }}</b-button>
        <b-button class="form_button" @click="saveElement()">{{ $t("buttons.save") }}</b-button>
      </div>
    </div>
  </div>
</template>
<script>
import "./Tool.css";

export default {
  name: "HeadingTool",
  props: ["formItemToEdit", "formItems", "closeTool"],
  data() {
    return {
      headingText: "",
      formItem: {},
      title: null,
      infoTextContent: "",
      toolStatus: false,
      textBoxTextError: false,
      textAreaTextError: false,
    };
  },
  created() {
    //Get the tool from the list of tools
    this.formItems.forEach((formItem) => {
      formItem.columnDetails.forEach((columnDetail) => {
        if (columnDetail.cellDetails.toolItemID == this.formItemToEdit) {
          this.formItem = columnDetail.cellDetails;
        }
      });
    });

    //Heading
    this.title = this.formItem.toolDetails.title;

    //Text Area Info Content
    if (this.formItem.toolDetails.infoTextContent) {
      this.infoTextContent = this.formItem.toolDetails.infoTextContent;
    }

    //Toolstatus
    if (this.formItem.toolDetails.toolStatus) {
      this.toolStatus = this.formItem.toolDetails.toolStatus;
    }
  },
  methods: {
    closeElement() {
      this.$emit("closeTool", this.formItem.toolItemID, this.formItem.toolDetails.toolStatus);
    },
    saveElement() {
      if (this.validate()) {
        this.formItem.toolDetails.title = this.title;
        this.formItem.toolDetails.infoTextContent = this.infoTextContent;
        this.formItem.toolDetails.toolStatus = this.toolStatus;

        this.$emit("saveElement", this.formItem);
      }
    },
    validate() {
      this.toolStatus = false;
      if (this.title == undefined || this.title == "") {
        this.textBoxTextError = true;
        return false;
      }
      if (this.infoTextContent == undefined || this.infoTextContent == "") {
        this.textAreaTextError = true;
        return false;
      }
      this.toolStatus = true;
      return true;
    },
  },
};
</script>
