<template>
  <div class="form_section">
    <div class="form_section_heading">{{ $t("tools.dependency") }}</div>
    <div v-show="!showDetails" class="form_section_detail form_section_loading">{{ $t("general.loading") }}</div>

    <div v-show="showDetails" class="form_section_detail">
      <label :for="parentField">{{ $t("tools.show_tool_if") }}</label>
      <div class="flex_container">
        <div class="flex_item">
          <b-form-select v-model="parentField" :options="toolList" @change="updateDependant($event, 'update')"></b-form-select>
        </div>

        <div class="flex_item">
          <div v-if="textComparison">
            <b-form-select v-model="comparison" :class="{ 'input-error': comparisonError }" :options="textComparisons" @change="updateComparison($event)"></b-form-select>
          </div>
          <div v-if="dateComparison">
            <b-form-select v-model="comparison" :class="{ 'input-error': comparisonError }" :options="dateComparisons" @change="updateComparison($event)"></b-form-select>
          </div>
          <div v-if="dropDownComparison">
            <b-form-select v-model="comparison" :class="{ 'input-error': comparisonError }" :options="dropDownComparisons" @change="updateComparison($event)"></b-form-select>
          </div>
          <div v-if="multiDropDownComparison">
            <b-form-select
              v-model="comparison"
              :class="{ 'input-error': comparisonError }"
              :options="multiSelectDropDownComparisons"
              @change="updateComparison($event)"
            ></b-form-select>
          </div>
        </div>
        <div class="flex_item">
          <div v-if="textCondtion">
            <b-form-input type="text" v-model="condition" :class="{ 'input-error': conditionError }" placeholder="Condition" @input="updateCondition()"></b-form-input>
          </div>
          <div v-if="ageCondtion">
            <b-form-input
              type="number"
              class="number_centre"
              v-model="condition"
              :class="{ 'input-error': conditionError }"
              placeholder="Condition"
              @input="updateCondition()"
            ></b-form-input>
          </div>
          <div v-show="dateCondtion">
            <input type="text" v-model="dateCondition" ref="dependancyDate" :class="{ 'input-error': conditionError }" placeholder="Condition" @input="updateCondition()" />
          </div>
          <div v-if="dropDownCondtion">
            <b-form-select v-model="condition" :class="{ 'input-error': conditionError }" :options="conditionList" @input="updateCondition()"></b-form-select>
          </div>
        </div>
      </div>
      <div class="flex_container">
        <div class="flex_item footnote">{{ $t("dependancies.message") }}</div>
      </div>
    </div>
  </div>
</template>
<script>
import "./Tool.css";
import { ref } from "vue";
import flatpickr from "flatpickr";

export default {
  name: "Dependency",
  props: ["dependency", "formItems", "formItem", "formSettings"],
  data() {
    return {
      textComparisons: [{ value: "", text: "Please select ..." }],
      dropDownComparisons: [
        { value: "=", text: "Selected choice is:" },
        { value: "!=", text: "Selected choice is not:" },
      ],
      multiSelectDropDownComparisons: [
        { value: "inc", text: "Selected includes:" },
        { value: "!inc", text: "Selected does not include:" },
      ],
      dateComparisons: [
        { value: "", text: "Please select ..." },
        { value: "=", text: "Equal to" },
        { value: "!=", text: "Not equal to" },
        { value: "<", text: "Less than" },
        { value: ">", text: "Greater than" },
        { value: "Age >", text: "Age greater than" },
        { value: "Age >=", text: "Age greater than or equal to" },
        { value: "Age <", text: "Age less than" },
        { value: "Age <=", text: "Age less than or equal to" },
      ],
      dateRangeComparisons: [{ value: "", text: "Between" }],

      toolList: [{ value: "", text: "Please select ..." }],
      conditionList: [{ value: "", text: "Please select ..." }],
      dependancyDate: ref(),
      parentField: "",
      comparison: "",
      condition: "",
      dateCondition: "",
      textComparison: true,
      dateComparison: false,
      dropDownComparison: false,
      multiDropDownComparison: false,
      textCondtion: true,
      ageCondtion: false,
      dateCondtion: false,
      dropDownCondtion: false,
      comparisonError: false,
      conditionError: false,
      showDetails: false,
    };
  },
  mounted() {
    new flatpickr(this.$refs.dependancyDate, { altInput: true, altFormat: this.dateFormat });
  },
  created() {
    if (this.dependency.parent) {
      this.parentField = this.dependency.parent.parentField;
      this.comparison = this.dependency.parent.comparison;
      this.condition = this.dependency.parent.condition;
    }

    const currentToolID = this.formItem.toolItemID;
    this.formItems.forEach((formItem) => {
      formItem.columnDetails.forEach((column) => {
        if (column.cellActive) {
          // Meet condtions to add to dependancy list
          var addTool = true;
          // Do not add itself
          if (currentToolID == column.cellDetails.toolItemID) {
            addTool = false;
          }
          // Do not add if the title is missing
          if (column.cellDetails.toolDetails.title == "") {
            addTool = false;
          }
          // Do not add headings or text area or text info fields
          if (column.cellDetails.toolTypeID == "1" || column.cellDetails.toolTypeID == "6" || column.cellDetails.toolTypeID == "7") {
            addTool = false;
          }
          // Do not add range types
          if (
            column.cellDetails.toolDetails.dateOptions == "DateRange" ||
            column.cellDetails.toolDetails.dateOptions == "DateRange2" ||
            column.cellDetails.toolDetails.dateOptions == "TimeRange"
          ) {
            addTool = false;
          }

          if (addTool == true) {
            // Doesn't add current tool or headings or tools with no titles
            const newTool = { value: column.cellDetails.toolItemID, text: column.cellDetails.toolDetails.title };
            this.toolList.push(newTool);
          }
        }
      });
    });

    //FormSettings
    if (this.formSettings) {
      if (this.formSettings.dateFormat) {
        this.dateFormat = this.formSettings.dateFormat;
      }
      if (this.formSettings.timeFormat) {
        if (this.formSettings.timeFormat == "24hr") {
          this.time_24hr = true;
          this.timeFormat = "H:i";
        } else {
          this.time_24hr = false;
          this.timeFormat = this.formSettings.timeFormat;
        }
      }
    }

    this.updateDependant(this.parentField, "initialise");
    this.showDetails = true;
  },
  methods: {
    updateDependant(parentValue, state) {
      var parentTool = {};
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((column) => {
          if (parentValue == column.cellDetails.toolItemID) {
            parentTool = column;
          }
        });
      });
      if (state == "update") {
        this.comparisonError = false;
        this.conditionError = false;
        this.comparison = "";
        this.condition = "";
        this.conditionList = [{ value: "", text: "Please select ..." }];
      }
      if (parentValue == "") {
        // None chosen
        this.textComparison = true;
        this.dateComparison = false;
        this.dropDownComparison = false;
        this.multiDropDownComparison = false;
        this.textComparisons = [{ value: "", text: "Please select ..." }];
        this.textCondtion = true;
        this.ageCondtion = false;
        this.dateCondtion = false;
        this.dropDownCondtion = false;
      } else {
        if (parentTool.cellDetails.toolTypeID == 2) {
          // Textbox
          this.textComparison = true;
          this.dateComparison = false;
          this.dropDownComparison = false;
          this.multiDropDownComparison = false;
          this.textComparisons = [
            { value: "", text: "Please select ..." },
            { value: "=", text: "Equal to" },
            { value: "!=", text: "Not equal to" },
            { value: "like", text: "Contains" },
          ];

          this.textCondtion = true;
          this.ageCondtion = false;
          this.dateCondtion = false;
          this.dropDownCondtion = false;
        }
        if (parentTool.cellDetails.toolTypeID == 4) {
          // Date
          this.textComparison = false;
          this.dateComparison = true;
          this.dropDownComparison = false;
          this.multiDropDownComparison = false;

          this.textCondtion = false;
          this.dropDownCondtion = false;
          if (this.comparison == "Age >" || this.comparison == "Age <" || this.comparison == "Age >=" || this.comparison == "Age <=") {
            this.ageCondtion = true;
            this.dateCondtion = false;
          } else {
            this.ageCondtion = false;
            this.dateCondtion = true;
          }
        }
        if (parentTool.cellDetails.toolTypeID == 3 || parentTool.cellDetails.toolTypeID == 5) {
          // Dropdown
          this.textComparison = false;
          this.dateComparison = false;
          if (parentTool.cellDetails.toolDetails.multipleSelect && parentTool.cellDetails.toolDetails.multipleSelect == true) {
            this.multiDropDownComparison = true;
            if (this.comparison == null || this.comparison == "=" || this.comparison == "!=") {
              this.comparison = "inc";
            }
          } else {
            this.dropDownComparison = true;
            if (this.comparison == null || this.comparison == "inc" || this.comparison == "!inc") {
              this.comparison = "=";
            }
          }
          this.textCondtion = false;
          this.ageCondtion = false;
          this.dateCondtion = false;
          this.dropDownCondtion = true;

          // Get the options for the comparsion
          if (parentTool.cellDetails.toolTypeID == 3) {
            this.conditionList = parentTool.cellDetails.toolDetails.dropDownOptions;
          }
          if (parentTool.cellDetails.toolTypeID == 5) {
            if (state == "update") {
              this.comparison = "=";
              this.condition = "";
            }
            this.conditionList = [
              { value: true, text: "Yes" },
              { value: false, text: "No" },
            ];
          }
        }
      }

      const dependency = { parentField: parentValue, comparison: this.comparison, condition: this.condition };
      this.$emit("updateDependency", dependency);
    },

    updateComparison(e) {
      if (this.dateComparison) {
        this.textCondtion = true;
        this.ageCondtion = false;
        this.dateCondtion = false;
      }

      if (this.dateComparison) {
        this.textCondtion = false;
        if (e == "Age >" || e == "Age <" || e == "Age >=" || e == "Age <=") {
          this.ageCondtion = true;
          this.dateCondtion = false;
        } else {
          this.ageCondtion = false;
          this.dateCondtion = true;
        }
      }

      const dependency = { parentField: this.parentField, comparison: e, condition: this.condition };
      this.$emit("updateDependency", dependency);
    },
    updateCondition() {
      const dependency = { parentField: this.parentField, comparison: this.comparison, condition: this.condition };
      this.$emit("updateDependency", dependency);
    },
    validate() {
      var valid = true;
      var parentTool;
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((column) => {
          if (this.parentField == column.cellDetails.toolItemID) {
            parentTool = column;
          }
        });
      });
      if (this.parentField != "") {
        // Resets error class to off
        this.comparisonError = false;
        this.comparisonError = false;

        var dependencyValid = true;
        if (this.comparison == "") {
          this.comparisonError = true;
          dependencyValid = false;
        }
        if (parentTool.cellDetails.toolTypeID == 4) {
          if (this.dateCondition === "") {
            this.conditionError = true;
            dependencyValid = false;
          }
        } else {
          if (this.condition === "") {
            this.conditionError = true;
            dependencyValid = false;
          }
        }

        if (dependencyValid == false) {
          valid = false;
        }
      }
      if (valid == false) {
        return false;
      }
      return true;
    },
  },
};
</script>
