<template>
  <div>Settings</div>
  <br /><br />

  <Draggable :list="list" :disabled="!enabled" item-key="name" class="list-group" ghost-class="ghost" @start="dragging = true" @end="dragging = false" v-bind="dragOptions">
    <template #item="{ element }">
      <div class="list-group-item" :class="{ 'not-draggable': !enabled }">
        {{ element.name }}
        <Draggable
          v-if="cellActive"
          :list="listRows"
          :disabled="!enabled"
          item-key="name"
          class="sub-list-group"
          ghost-class="ghost"
          @start="
            show(this);
            dragging = true;
          "
          @end="dragging = false"
          v-bind="dragOptions"
        >
          <template #item="{ element }">
            <div class="sub-list-group-item" :class="{ 'not-draggable': !enabled }">
              {{ element.name }}
            </div>
          </template>
        </Draggable>
      </div>
    </template>
  </Draggable>

  <div class="json_list details">
    <div>
      <b-button class="form_button" @click="publishAll()">{{ $t("buttons.publish_all") }}</b-button>
      {{ published }}
    </div>
    <b>Source:</b> {{ baseUrl }}<br /><br />
    <div v-for="form in forms" :key="form.form_id">
      Form: {{ form.form_id }} - {{ form.form_name }} - {{ form.form_status }}
      <pre>{{ JSON.parse(form.form_content) }}</pre>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import Draggable from "vuedraggable";

export default {
  name: "Settings",
  components: { Draggable },
  data() {
    return {
      enabled: true,
      list: [
        { name: "List1", id: 0 },
        { name: "List2", id: 1 },
        { name: "List3", id: 2 },
      ],
      dragging: false,
      cellActive: true,
      drag: false,
      dateFormat: "j F Y",
      published: null,
      url: process.env.VUE_APP_CM_STORE + "/api/rest.php",
      baseUrl: process.env.VUE_APP_CM_STORE,
      headers: { "Content-Type": "application/json" },
      postdata: {
        module: "forms",
        details: {},
      },
      forms: [],
      listRows: [
        { id: 1, name: "Item 1" },
        { id: 2, name: "Item 2" },
        { id: 3, name: "Item 3" },
      ],
    };
  },
  computed: {
    dragOptions() {
      return {
        animation: 350,
        group: "description",
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
  created() {
    // Prevent background scroll
    document.body.style.overflow = "scroll";

    //Get the available target fields
    this.postdata.action = "all_forms";
    Axios.post(this.url, this.postdata, this.headers).then((res) => {
      res.data.forEach((value) => {
        this.forms.push(value);
      });
    });
  },
  methods: {
    show(e) {
      console.log(e.dragging);
    },
    publishAll() {
      this.published = "";
      this.postdata.action = "publish_all";
      Axios.post(this.url, this.postdata, this.headers).then((res) => {
        this.published = res.data;
      });
    },
  },
};
</script>
<style scoped>
.body {
  overflow: scroll;
}
.drag-list {
  width: 20%;
  margin: auto;
}
.details {
  text-align: left;
}
pre {
  white-space: pre-wrap; /* Since CSS 2.1 */
  white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
  white-space: -pre-wrap; /* Opera 4-6 */
  white-space: -o-pre-wrap; /* Opera 7 */
  word-wrap: break-word; /* Internet Explorer 5.5+ */
}
.button {
  margin-top: 35px;
}

.flip-list-move {
  transition: transform 1.5s;
}

.no-move {
  transition: transform 1s;
}

.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}

.list-group {
  min-height: 20px;
}

.list-group-item {
  margin: 20px auto;
  cursor: move;
  background-color: red;
}
.sub-list-group-item {
  background-color: lightblue;
}

.list-group-item i {
  cursor: pointer;
}
.json_list {
  margin-left: 20%;
}
</style>
