<template>
  <div class="confirm_backdrop">
    <div class="message_modal">
      <div class="form_header">Confirm</div>
      <div class="form_body confirm_message">
        <div v-if="confirmOrigin != 'fieldsInUse'">
          {{ this.confirmMessage }}
          <div v-if="this.terms" class="terms" :class="{ terms_error: termsConfirmedError }">
            <input type="checkbox" v-model="termsConfirmed" @click="termsChanged()" /> {{ this.terms }}
          </div>
        </div>

        <div v-if="confirmOrigin == 'fieldsInUse'">
          <table class="table_in_use">
            <tr>
              <td class="table_margin"></td>
              <td class="table_message">{{ this.confirmMessage }}</td>
              <td class="table_margin"></td>
            </tr>
            <tr>
              <td class="table_margin"></td>
              <td class="table_message table_margin_list">
                <li v-for="item in items" :key="item.form">
                  {{ item.form }}
                </li>
              </td>
              <td class="table_margin_list"></td>
            </tr>
          </table>
        </div>
      </div>

      <div class="form_footer">
        <div v-if="confirmOrigin == 'info'">
          <b-button class="form_button" @click="closeContinue('N')">Close</b-button>
        </div>
        <div v-else-if="confirmOrigin == 'preview'">
          <b-button class="form_button" @click="closeContinue('N')">Close</b-button>
        </div>
        <div v-else-if="confirmOrigin == 'fieldsInUse'">
          <b-button class="form_button" @click="closeContinue('N')">Close</b-button>
        </div>
        <div v-else>
          <b-button class="form_button" @click="closeContinue('N')">Cancel</b-button>
          <b-button class="form_button" @click="closeContinue('Y')">Yes</b-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Confirm",
  props: ["origin", "confirmMessage", "terms", "fieldInUseList"],
  components: {},
  data() {
    return {
      confirmOrigin: this.origin,
      items: this.fieldInUseList,
      termsConfirmed: null,
      termsConfirmedError: false,
    };
  },
  methods: {
    termsChanged() {
      this.termsConfirmedError = false;
    },

    closeContinue(e) {
      if (e == "Y") {
        if (this.terms) {
          if (this.termsConfirmed) {
            this.$emit("closeContinue", e, this.origin);
          } else {
            this.termsConfirmedError = true;
          }
        } else {
          this.$emit("closeContinue", e, this.origin);
        }
      } else {
        this.$emit("closeContinue", e, this.origin);
      }
    },
  },
};
</script>

<style scoped>
.confirm_backdrop {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 998;
  background: rgba(0, 0, 0, 0.2);
}
.confirm_message {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.terms {
  margin-top: 30px;
  margin-left: 20px;
}
.terms_error {
  color: red;
}
.table_in_use {
  width: 100%;
}
.table_message {
  width: 50%;
  text-align: left;
}
.table_margin {
  width: 25%;
  text-align: left;
}

.table_margin_list {
  padding-left: 15px;
}
</style>
