<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">
        {{ $t("tools.check_list") }}
      </div>
      <div class="form_body">
        <TargetField :target="target" :formItem="formItem" :formItems="formItems" :targetSelectError="targetSelectError" @updateTarget="updateTarget" ref="tagretRef" />

        <div class="form_section">
          <div class="form_section_heading">{{ $t("tools.preview") }}</div>
          <div class="form_section_detail">
            <div v-if="inputFieldText">
              {{ inputFieldText }}
            </div>
            <div v-else>{{ $t("tools.label") }}</div>
            <b-form-input :disabled="true"></b-form-input>
          </div>
        </div>

        <div class="form_section">
          <div class="form_section_heading">{{ $t("tools.label") }}</div>
          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.label_text") }}</label>
            <b-form-input v-model="inputFieldText" :placeholder="$t('place_holders.label_text')" :class="{ 'input-error': inputFieldTextError }"></b-form-input>
          </div>
          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.tool_tip") }}</label>
            <b-form-input v-model="toolTipText" :placeholder="$t('place_holders.tool_tip')"></b-form-input>
          </div>
        </div>

        <div class="form_section" v-if="target.required == 'Y'">
          <div class="form_section_heading">{{ $t("tools.validation") }}</div>
          <div class="form_section_detail">
            <div class="flex_container">
              <div class="flex_item">
                <label :for="minLength">{{ $t("tools.min_length") }}</label>
                <b-form-input v-model="minLength" type="number" min="1" max="255" @input="checkRangeMin()"></b-form-input>
              </div>
              <div class="flex_item">
                <label :for="minLength">{{ $t("tools.error_message") }}</label
                ><br />
                <b-form-input v-model="minLengthError" type="text"></b-form-input>
              </div>
            </div>
            <div class="flex_container">
              <div class="flex_item">
                <label :for="minLength">{{ $t("tools.max_length") }}</label
                ><br />
                <b-form-input v-model="maxLength" type="number" class="number_centre" min="1" max="255" @input="checkRangeMax()"></b-form-input>
              </div>
              <div class="flex_item">
                <label :for="minLength">{{ $t("tools.error_message") }}</label
                ><br />
                <b-form-input v-model="maxLengthError" type="text"></b-form-input>
              </div>
            </div>
            <div class="flex_container">
              <div class="flex_item">
                <label :for="minLength">{{ $t("tools.custom_format") }}</label>
                <b-form-input v-model="textFormat" type="text" value="" disabled></b-form-input>
              </div>
              <div class="flex_item">
                <label :for="minLength">{{ $t("tools.error_message") }}</label
                ><br />
                <b-form-input v-model="textFormatError" type="text" disabled></b-form-input>
              </div>
            </div>
          </div>
        </div>

        <div class="form_section">
          <div class="form_section_heading">{{ $t("general.default_value") }}</div>
          <div class="form_section_detail">
            <div>
              <label :for="defaultValue">{{ $t("general.default_value") }}</label>
              <b-form-input v-model="defaultValue" type="text" @input="checkDefaultValue()"></b-form-input>
            </div>
          </div>
        </div>
        <Dependency :dependency="dependency" :formItem="formItem" :formItems="formItems" :formSettings="formSettings" @updateDependency="updateDependency" ref="dependencyRef" />
      </div>
      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">{{ $t("buttons.cancel") }}</b-button>
        <b-button class="form_button" @click="saveElement()">{{ $t("buttons.save") }}</b-button>
        <div>{{ message }}</div>
      </div>
    </div>
  </div>
</template>
<script>
import "./Tool.css";
import Dependency from "./Dependency.vue";
import TargetField from "./TargetField.vue";
import { ref } from "vue";

export default {
  name: "InputFieldTool",
  components: { Dependency, TargetField },
  emits: ["dependency", "target", "saveElement", "closeTool"],
  props: ["formItemToEdit", "formItems", "formSettings"],
  data() {
    return {
      rowItem: this.rowToEdit,
      formItem: {},
      dependencyRef: ref(),
      tagretRef: ref(),
      dependency: {},
      target: {},
      message: "",
      // General
      inputFieldText: "",
      toolStatus: null,
      defaultValue: "",
      toolTipText: "",
      // Validation
      minLength: 1,
      maxLength: 50,
      minLengthError: "Too short",
      maxLengthError: "Too long",
      textFormat: "This feature coming soon ....",
      textFormatError: "Invalid format",
      inputFieldTextError: false,
      targetSelectError: false,
    };
  },
  created() {
    //Get the tool from the list of tools
    this.formItems.forEach((formItem) => {
      formItem.columnDetails.forEach((columnDetail) => {
        if (columnDetail.cellDetails.toolItemID == this.formItemToEdit) {
          this.formItem = columnDetail.cellDetails;
        }
      });
    });
    //Load the saved tool details

    // Label and tool tip
    this.inputFieldText = this.formItem.toolDetails.title;
    if (this.formItem.toolDetails.toolTipText) {
      this.toolTipText = this.formItem.toolDetails.toolTipText;
    }
    // Parent details
    if (this.formItem.toolDetails.parent) {
      this.dependency.parent = this.formItem.toolDetails.parent;
    }
    // Target details
    if (this.formItem.toolDetails.target) {
      this.target = this.formItem.toolDetails.target;
    }
    // Default details
    if (this.formItem.toolDetails.default) {
      this.defaultValue = this.formItem.toolDetails.default;
    }
    // Validation details
    if (this.formItem.toolDetails.validation) {
      // this.minLength = this.formItem.toolDetails.validation.minLength;
    }
    // Toolstatus
    if (this.formItem.toolDetails.toolStatus) {
      this.toolStatus = this.formItem.toolDetails.toolStatus;
    }
  },
  methods: {
    updateDependency(dependency) {
      this.dependency.parent = dependency;
    },
    updateTarget(target) {
      target.defaultValue ? (this.defaultValue = target.defaultValue) : (this.defaultValue = "");
      this.target = target;
    },
    closeElement() {
      this.rowItem = this.rowToEdit;
      this.$emit("closeTool", this.formItem.toolItemID, this.formItem.toolDetails.toolStatus);
    },
    saveElement() {
      if (this.validate()) {
        this.message = "";

        this.formItem.toolDetails.title = this.inputFieldText;
        this.formItem.toolDetails.toolTipText = this.toolTipText;

        if (this.dependency.parent !== undefined) {
          if (this.dependency.parent.parentField === "") {
            delete this.formItem.toolDetails.parent;
          } else {
            this.formItem.toolDetails.parent = this.dependency.parent;
          }
        }

        var validation = {
          minLength: this.minLength,
        };
        this.formItem.toolDetails.validation = validation;
        this.formItem.toolDetails.target = this.target;
        this.formItem.toolDetails.toolStatus = this.toolStatus;
        this.formItem.toolDetails.default = this.defaultValue;
        this.$emit("saveElement", this.formItem);
      }
    },
    validate() {
      var valid = true;
      this.toolStatus = false;
      this.inputFieldTextError = false;
      this.targetSelectError = false;

      if (this.inputFieldText == undefined || this.inputFieldText == "") {
        this.inputFieldTextError = true;
        valid = false;
      }
      if (this.target.targetField == undefined) {
        this.targetSelectError = true;
        valid = false;
      }

      // Validate the dependency
      if (this.$refs.dependencyRef.validate() == false) {
        valid = false;
      }

      if (valid == false) {
        return false;
      }
      this.toolStatus = true;
      return true;
    },
  },
};
</script>
