<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">Form Preview & Publish</div>
      <div class="form_body preview_form_body">
        <b-button id="view_button" class="form_button" @click="changeView()"> {{ viewType }}</b-button>
        <div v-show="showEdit && !loading">
          <h5>Current Edit</h5>
          <div class="custom_form" v-html="html"></div>
        </div>
        <div v-show="!showEdit && !loading">
          <h5>Published Form</h5>
          <div class="custom_form flex-item" v-html="htmlPublished"></div>
        </div>
        <div v-show="loading">{{ $t("general.loading") }}</div>
        <div v-show="showEdit && !loading">
          <div :class="{ form_button_hidden: published }"><b-button class="form_button" @click="publishForm()">Publish</b-button></div>
        </div>
        <div v-show="!showEdit && !loading">
          <div v-show="lastPublished">
            <div>
              <b-button class="form_button" @click="enableDisable()">{{ enableAction }}</b-button>
            </div>
            <div><h5>Status</h5></div>
            <div class="enabled-check">
              <div>{{ enabledStatus }}</div>
            </div>
            <div class="enabled-check"></div>

            <div>Last published: {{ lastPublished }}</div>
            <div>{{ message }}</div>
          </div>
        </div>
      </div>

      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">Close</b-button>
      </div>
      <div v-if="showConfirmContinue">
        <ConfirmContinue :origin="origin" :confirmMessage="confirmMessage" :terms="terms" @closeContinue="closeContinue" />
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import ConfirmContinue from "./ConfirmContinue.vue";

export default {
  name: "Preview",
  components: { ConfirmContinue },
  props: ["formID", "formName", "formSettings", "fullForm", "formStatus", "formEnabled"],
  data() {
    return {
      html: "Loading ....",
      htmlPublished: "Loading ....",
      js: "",
      url: process.env.VUE_APP_CM_STORE + "/api/rest.php",
      headers: { "Content-Type": "application/json" },
      lastPublished: null,
      enabled: this.formEnabled,
      published: false,
      loading: true,
      message: "",
      showConfirmContinue: false,
      enableAction: null,
      enabledStatus: null,
      confirmMessage: null,
      terms: null,
      origin: "preview",
      elementDraft: null,
      elementPublished: null,
      showEdit: true,
      viewType: "View published form",
    };
  },
  mounted() {
    setTimeout(() => {
      this.datepicker = document.createElement("script");
      this.datepicker.src = "https://cdn.jsdelivr.net/npm/flatpickr";
      this.datepicker.innerHTML = "//CM_DATEPICKER";
      document.documentElement.appendChild(this.datepicker);
    }, 1000);

    this.getForm("dra");
  },
  methods: {
    closeContinue(e) {
      this.showConfirmContinue = false;
      if (e == "Y") {
        this.publishFormConfirmed();
      }
    },
    changeView() {
      if (this.showEdit == true) {
        this.viewType = "View current edit";
        this.showEdit = false;
        this.getForm("pub");
      } else {
        this.viewType = "View published form";
        this.showEdit = true;
        this.getForm("dra");
      }
    },
    getForm(type) {
      this.removeChildren("preview");
      this.loading = true;

      if (type == "dra") {
        const postdata = {
          module: "forms",
          action: "render_html",
          details: { formID: this.formID, formName: this.formName, formSettings: this.formSettings, formDetails: this.fullForm },
        };
        Axios.post(this.url, postdata, this.headers).then((res) => {
          // Set tooltip image
          this.html = res.data.html;
          const imgSrc = require(`@/assets/icons/${"form-tool-tip.svg"}`);
          this.html = this.html.replaceAll("{{tool-tip-image}}", imgSrc);
          this.html = this.html.replaceAll("{{form_button_text}}", "Submit");
          this.js = res.data.js;
          this.validation = res.data.validation;
          setTimeout(() => {
            this.elementDraft = document.createElement("script");
            this.elementDraft.innerHTML = this.js + this.validation;
            document.documentElement.appendChild(this.elementDraft);
            this.loading = false;
          }, 2000);
        });
      }
      if (type == "pub") {
        const postdata = {
          module: "forms",
          action: "get_published",
          details: { formID: this.formID },
        };
        Axios.post(this.url, postdata, this.headers).then((res) => {
          if (res.data.form_published == "None") {
            this.htmlPublished = "This form is yet to be published";
            this.enabled = false;
            this.enabledStatus = "Not enabled in store";
            this.enableAction = "Enable";
            this.loading = false;
          } else {
            this.htmlPublished = res.data.html;
            const imgSrc = require(`@/assets/icons/${"form-tool-tip.svg"}`);
            this.htmlPublished = this.htmlPublished.replaceAll("{{tool-tip-image}}", imgSrc);
            this.js = res.data.js;
            this.validation = res.data.validation;

            setTimeout(() => {
              this.elementPublished = document.createElement("script");
              this.elementPublished.innerHTML = this.js + this.validation;
              document.documentElement.appendChild(this.elementPublished);
              this.loading = false;
            }, 2000);

            this.lastPublished = res.data.last_published;
            if (this.enabled) {
              this.enableAction = "Disable";
              this.enabledStatus = "Enabled in store";
            } else {
              this.enableAction = "Enable";
              this.enabledStatus = "Not enabled in store";
            }
          }
        });
      }
    },
    publishForm() {
      if (this.formStatus == false) {
        this.origin = "preview";
        this.confirmMessage = "The form has elements that are not complete.";
        this.showConfirmContinue = true;
      } else {
        this.origin = "publish";
        this.confirmMessage = "Are you sure that you would like to publish this form?";
        this.terms = "Please confirm that you are not storing any sensitive or personal data with this form.";
        this.showConfirmContinue = true;
      }
    },
    publishFormConfirmed() {
      this.loading = true;
      const postdata = {
        module: "forms",
        action: "publish_form",
        details: { formID: this.formID },
      };
      Axios.post(this.url, postdata, this.headers).then((res) => {
        this.published = true;
        this.viewType = "View current edit";
        this.showEdit = false;
        this.message = "Successfully published";
        this.lastPublished = res.data.lastPublished;
        this.htmlPublished = res.data.html;
        const imgSrc = require(`@/assets/icons/${"form-tool-tip.svg"}`);
        this.htmlPublished = this.htmlPublished.replaceAll("{{tool-tip-image}}", imgSrc);
        this.js = res.data.js;
        this.validation = res.data.validation;
        setTimeout(() => {
          this.elementPublished = document.createElement("script");
          this.elementPublished.innerHTML = this.js + this.validation;
          document.documentElement.appendChild(this.elementPublished);
          this.$emit("updatePublished", this.lastPublished, this.enabled);
          this.loading = false;
        }, 2000);

        if (this.enabled) {
          this.enableAction = "Disable";
          this.enabledStatus = "Enabled in store";
        } else {
          this.enableAction = "Enable";
          this.enabledStatus = "Not enabled in store";
        }
      });
    },
    enableDisable() {
      this.enabled = !this.enabled;

      if (this.enabled) {
        this.enableAction = "Disable";
        this.enabledStatus = "Enabled in store";
      } else {
        this.enableAction = "Enable";
        this.enabledStatus = "Not enabled in store";
      }
      const postdata = {
        module: "forms",
        action: "set_enabled",
        details: { formID: this.formID, formEnabled: this.enabled },
      };
      Axios.post(this.url, postdata, this.headers).then(() => {
        this.$emit("updatePublished", this.lastPublished, this.enabled);
      });
    },
    closeElement() {
      if (this.published == true) {
        document.documentElement.removeChild(this.elementPublished);
      }
      this.removeChildren("close");
      this.$emit("closeModal");
    },
    removeChildren(type) {
      //Remove js children
      var children = document.documentElement.children;
      var deleted = 0;
      for (var i = 0; i < children.length; i++) {
        var child = children[i];
        if (child.innerHTML.includes("CM_JAVASCRIPT")) {
          document.documentElement.removeChild(child);
          deleted++;
        }
        if (child.innerHTML.includes("CM_DATEPICKER") && type == "close") {
          document.documentElement.removeChild(child);
          deleted++;
        }
      }
      if (deleted > 0) {
        this.removeChildren(type);
      }
    },
  },
};
</script>

<style scoped>
.preview_form_body {
  padding-left: 15px;
}
#view_button {
  width: 200px;
}
.custom_form :deep(.form_main) {
  text-align: left;
  padding: 20px;
  background: white;
  min-height: 300px;
  max-height: 350px;
  overflow-y: auto;
}
.custom_form :deep(.form_label) {
  margin-top: 20px;
}
.custom_form :deep(.form_input) {
  width: 100%;
  height: 30px;
}
.custom_form :deep(.form_select) {
  width: 100%;
  height: 30px;
}
.custom_form :deep(.form_mulitple_select) {
  width: 100%;
}
.custom_form :deep(.form_submit) {
  margin-top: 20px;
}
.custom_form :deep(.tool_required),
:deep(.error_message) {
  color: #ff3718;
}
.flex-item {
  flex: 1;
}
.enabled-check {
  display: flex;
  justify-content: center;
}
</style>
