<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">Field Mappings</div>
      <div class="form_body">
        <div class="field_mapping_table">
          <b-table striped hover :items="formFields" :fields="fields" responsive="sm" sort-icon-left>
            <template #head(fieldName)="data"> {{ $t(data.label) }}</template>
            <template #head(fieldType)="data"> {{ $t(data.label) }}</template>
            <template #head(FieldSourceRecord)="data"> {{ $t(data.label) }}</template>
            <template #head(default_value)="data"> {{ $t(data.label) }}</template>
            <template #head(formDefaultValue)="data"> {{ $t(data.label) }}</template>
            <!-- <template #head(FieldTargetField)="data"> {{ $t(data.label) }}</template> -->
            <template #head(saveToCM)="data"> {{ $t(data.label) }}</template>
            <template #head(saveToLms)="data"> {{ $t(data.label) }}</template>
            <template #head(savedToCrm)="data"> {{ $t(data.label) }}</template>

            <template v-slot:cell(savedToCrm)="{ item }">
              <span :class="{ text_red: item.savedToCrm == 'Invalid field name' }">
                {{ item.savedToCrm }}
              </span>
            </template>

            <!-- <template #cell(updateLMS)="row">
              <b-button size="sm" @click="updateLMS(row)" class="mr-2"> LMS </b-button>
            </template>
            <template #cell(updateCRM)="row">
              <b-button size="sm" @click="updateCRM(row)" class="mr-2"> CRM </b-button>
            </template> -->
          </b-table>
        </div>
      </div>

      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">Close</b-button>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
// import ConfirmContinue from "./ConfirmContinue.vue";

export default {
  name: "Preview",
  components: {},
  props: ["formID", "formName", "formSettings", "fullForm", "formStatus", "formEnabled"],
  data() {
    return {
      formFields: [],
      fields: [
        { key: "fieldName", label: "table_heading.field_name", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "fieldType", label: "table_heading.field_type", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "FieldSourceRecord", label: "table_heading.field_form_source", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "default_value", label: "table_heading.field_default_value", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "formDefaultValue", label: "table_heading.field_form_default_value", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        // { key: "FieldTargetField", label: "table_heading.field_form_target", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "saveToCM", label: "table_heading.field_form_cm_target", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "saveToLms", label: "table_heading.field_form_lms_target", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "savedToCrm", label: "table_heading.field_form_crm_target", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
      ],
      mappings: null,
      url: process.env.VUE_APP_CM_STORE + "/api/rest.php",
      headers: { "Content-Type": "application/json" },

      postdata: {
        module: "forms",
        details: {},
      },
    };
  },

  created() {
    // var valid = true;
    // var duplicate = false;

    this.fullForm.forEach((section) => {
      section.columnDetails.forEach((column) => {
        if (column.cellDetails.toolDetails && column.cellDetails.toolDetails.target) {
          // this.loading = true;
          this.postdata.action = "field_by_name";
          this.postdata.details = { fieldName: column.cellDetails.toolDetails.target.targetField };
          Axios.post(this.url, this.postdata, this.headers).then((res) => {
            res.data.forEach((value) => {
              // Check for writing to CM
              var saveToCM = false;
              // saveToCM = value.FieldTargetRecord.substring(0, 3) == "CM_" ? value.FieldTargetRecord + "::" + value.FieldTargetField : false;
              saveToCM = value.FieldTargetRecord.substring(0, 3) == "CM_" ? value.FieldTargetField : false;
              if (saveToCM == false) {
                // saveToCM = value.FieldCopyRecord.substring(0, 3) == "CM_" ? value.FieldCopyRecord + "::" + value.FieldCopyField : "---";
                saveToCM = value.FieldCopyRecord.substring(0, 3) == "CM_" ? value.FieldCopyField : "---";
              }

              value.saveToCM = saveToCM;

              // Check for writing to LMS
              var saveToLms = false;
              saveToLms = value.FieldTargetRecord.substring(0, 4) == "LMS_" ? value.FieldTargetRecord + "::" + value.FieldTargetField : "---";
              value.saveToLms = saveToLms;

              value.formDefaultValue = column.cellDetails.toolDetails.default;

              if (value.FieldSourceRecord != "") {
                value.FieldSourceRecord = value.FieldSourceRecord + "::" + value.FieldSourceField;
              }
              this.formFields.push(value);
            });
          });
        }
      });
    });

    this.loading = false;
  },
  methods: {
    closeContinue(e) {
      this.showConfirmContinue = false;
      if (e == "Y") {
        this.publishFormConfirmed();
      }
    },
    updateLMS(row) {
      console.log(row);
    },
    updateCRM(row) {
      console.log(row);
    },

    closeElement() {
      this.$emit("closeModal");
    },
  },
};
</script>

<style scoped>
.text_red {
  color: red;
}
.field_mapping_table {
  font-size: small;
}
</style>
