<template>
  <div v-if="showMappings">
    <Mappings
      :fullForm="formItems"
      :formID="this.formID"
      :formName="this.formName"
      :formSettings="this.formSettings"
      :formEnabled="this.formEnabled"
      :formStatus="this.formStatus"
      @updatePublished="updateStatus"
      @closeModal="closeModal()"
    />
  </div>
  <div v-if="showPreview">
    <Preview
      :fullForm="formItems"
      :formID="this.formID"
      :formName="this.formName"
      :formSettings="this.formSettings"
      :formEnabled="this.formEnabled"
      :formStatus="this.formStatus"
      @updatePublished="updateStatus"
      @closeModal="closeModal()"
    />
  </div>
  <div v-if="showLoad">
    <LoadForm @loadForm="loadForm" @closeModal="closeModal()" @formDeleted="clearForm" />
  </div>
  <div v-if="showConfirmContinue">
    <ConfirmContinue :origin="origin" :confirmMessage="confirmMessage" @closeContinue="closeContinue" />
  </div>

  <div v-if="showSettings">
    <FormSettings :formSettings="formSettings" @closeTool="closeTool" @saveSettings="saveSettings" />
  </div>
  <div v-if="showHeading">
    <Heading :formItemToEdit="formItemToEdit" :formItems="formItems" @closeTool="closeTool" @saveElement="saveElement" />
  </div>
  <div v-if="showInfoText">
    <InfoText :formItemToEdit="formItemToEdit" :formItems="formItems" @closeTool="closeTool" @saveElement="saveElement" />
  </div>
  <div v-if="showInputField">
    <InputField :formItemToEdit="formItemToEdit" :formItems="formItems" @closeTool="closeTool" @saveElement="saveElement" />
  </div>
  <div v-if="showDropdown">
    <Dropdown :formItemToEdit="formItemToEdit" :formItems="formItems" @closeTool="closeTool" @saveElement="saveElement" />
  </div>
  <div v-if="showDateField">
    <DateField :formItemToEdit="formItemToEdit" :formItems="formItems" :formSettings="formSettings" @closeTool="closeTool" @saveElement="saveElement" />
  </div>
  <div v-if="showBooleanField">
    <BooleanField :formItemToEdit="formItemToEdit" :formItems="formItems" @closeTool="closeTool" @saveElement="saveElement" />
  </div>
  <div v-if="showTextArea">
    <TextArea :formItemToEdit="formItemToEdit" :formItems="formItems" @closeTool="closeTool" @saveElement="saveElement" />
  </div>
  <div v-if="showCheckList">
    <CheckList :formItemToEdit="formItemToEdit" :formItems="formItems" @closeTool="closeTool" @saveElement="saveElement" />
  </div>

  <div class="main">
    <div class="form_heading">
      <div>
        <h4>{{ $t("custom_forms") }}</h4>
      </div>
      <div>
        <b-button class="form_button form_button_hidden" :class="{ form_button_show: formSaved && formItems.length > 0 }" @click="mappings()">{{
          $t("buttons.mappings")
        }}</b-button>
        <b-button class="form_button form_button_hidden" :class="{ form_button_show: formSaved && formItems.length > 0 }" @click="previewForm()">{{
          $t("buttons.preview")
        }}</b-button>
        <b-button class="form_button form_button_hidden" :class="{ form_button_save: saveColour, form_button_show: formLoaded }" @click="saveForm('save')">{{
          $t("buttons.save")
        }}</b-button>
        <b-button class="form_button" @click="viewSavedForms()">{{ $t("buttons.load") }}</b-button>
        <b-button class="form_button" @click="newForm()">{{ $t("buttons.new_form") }}</b-button>
      </div>
    </div>

    <div class="form_nav">
      <div class="form_nav_name">
        <div class="form_heading">
          <b-form-input v-model="formName" :placeholder="$t('place_holders.form_name')" class="form_name" @input="setEdited" :class="{ tool_error: formNameError }"></b-form-input>
          <!-- <b-form-input v-model="formDescription" placeholder="Description" class="form_name" @input="setEdited" :class="{ tool_error: formDescriptionError }"></b-form-input> -->
        </div>
      </div>
      <div class="form_nav_message">
        {{ message }}
      </div>
    </div>

    <div class="form_area" @dblclick.stop="addSection()">
      <div id="form_design" class="form_design" @dragover.prevent @drop.stop.prevent="onDropSection($event)">
        <Draggable :list="formItems" item-key="sectionRow" handle=".section_handle" class="list-group flip-list-move" @end="rowMoved()" v-bind="dragOptions">
          <template #item="{ element }">
            <div class="list-group-item tool_section">
              <div class="section-edit" @click="editSection(`${element.sectionRow}`)">
                <img src="@/assets/icons/form-add-section.svg" class="section_edit_icon" />
              </div>
              <div class="section_handle">
                <b>::</b>
                <!-- <img src="@/assets/icons/form-up-down.svg" class="section_handle_icon" /> -->
              </div>

              <div class="form_section_tool">
                <div
                  class="form_section_tool_item"
                  :id="'row_' + element.sectionRow + '_col_' + section_tool.sectionColumn"
                  v-for="section_tool in element.columnDetails"
                  :key="section_tool.col"
                  @dragover.prevent
                  @drop.stop.prevent="onToolDrop($event)"
                  @dblclick.stop="editSection(`${element.sectionRow}`)"
                >
                  <div class="row_cell">
                    <!-- Shows the delete section X if the cell is empty (ie not active) -->
                    <div
                      v-if="section_tool.cellActive == false"
                      class="section-delete"
                      @click="deleteSection(`${'row_' + element.sectionRow + '_col_' + section_tool.sectionColumn}`)"
                    >
                      <img src="@/assets/icons/form-delete-section.svg" class="section_edit_icon" />
                    </div>
                    <!-- If it is active it will have a tool inside. Details of the tool shown. Red outline if the tool is not complete -->
                    <div
                      v-if="section_tool.cellActive"
                      :class="{ tool_error: !section_tool.cellDetails.toolDetails.toolStatus }"
                      class="tool_design"
                      draggable="true"
                      handle=".tool-drag"
                      :id="'row_' + element.sectionRow + '_col_' + section_tool.sectionColumn + '_cell'"
                      @dragstart="startFormToolDrag($event, section_tool)"
                      @click="editTool(`${section_tool.cellDetails.toolItemID}`)"
                      @drag="toolOnDrag($event)"
                    >
                      <div class="form_title">
                        <!-- The name of the tool -->
                        <b
                          ><u>{{ $t(section_tool.cellDetails.toolName) }}</u></b
                        >
                      </div>
                      <div>
                        <!-- The tool's label -->
                        {{ displayLabelByLength(`${section_tool.cellDetails.toolTypeID}`, section_tool.cellDetails.toolDetails.title) }}
                      </div>
                      <div v-if="section_tool.cellDetails.toolDetails.subheading != ''">
                        <!-- The tool's subheading -->
                        {{ displaySubheadingByLength(section_tool.cellDetails.toolDetails.subheading) }}
                      </div>
                      <div class="form_tool_details" v-if="section_tool.cellDetails.toolDetails.target">
                        {{ $t("tools.target_field") }}: "{{ section_tool.cellDetails.toolDetails.target.targetField }}"

                        <!--Display for required -->
                        <div v-if="section_tool.cellDetails.toolDetails.target.required">
                          <!-- Different heading for Yes No -->
                          <div v-if="section_tool.cellDetails.toolTypeID == 5">
                            : {{ $t("tools.required_boolean") }}: "{{ section_tool.cellDetails.toolDetails.target.required == "Y" ? $t("general.yes") : $t("general.no") }}"
                          </div>
                          <div v-else>
                            : {{ $t("tools.required_boolean") }}: "{{ section_tool.cellDetails.toolDetails.target.required == "Y" ? $t("general.yes") : $t("general.no") }}"
                          </div>
                        </div>

                        <!--Display for default -->
                        <div v-if="section_tool.cellDetails.toolDetails.default || section_tool.cellDetails.toolDetails.default == false" class="default_value">
                          <!-- Different heading for Yes No -->
                          <div v-if="section_tool.cellDetails.toolTypeID == 5">
                            {{ $t("general.default_value") }}: "{{ section_tool.cellDetails.toolDetails.default == true ? $t("general.yes") : $t("general.no") }}"
                          </div>
                          <div v-else-if="section_tool.cellDetails.toolDetails.default != ''">
                            {{ $t("general.default_value") }}: "{{ section_tool.cellDetails.toolDetails.default }}"
                          </div>
                        </div>
                      </div>

                      <!--Display for dependancies -->
                      <div class="form_tool_details" v-if="section_tool.cellDetails.toolDetails.parent && section_tool.cellDetails.toolDetails.parent.parentField != ''">
                        {{ $t("tools.show_if") }}: "{{ getDependantName(section_tool.cellDetails.toolDetails.parent.parentField) }}" :
                        {{ getDependantComparison(section_tool.cellDetails.toolDetails.parent.comparison) }} :
                        {{ getDependantCondition(section_tool.cellDetails.toolDetails.parent) }}
                      </div>

                      <div class="tool_delete" @click.stop="deleteTool(`${section_tool.cellDetails.toolItemID}`)">
                        <!-- <img src="@/assets/icons/form-delete-section.svg" class="tool_delete_icon" /> -->
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </template>
        </Draggable>
      </div>
      <div class="form_tools" v-if="this.formLoaded">
        <div class="tool_settings" @click="openSettings()">
          <div class="flex_container tool_details">
            <div>
              <img :src="require(`@/assets/icons/form-settings.svg`)" class="tool_icon" />
            </div>
            <div class="tool_name">
              {{ $t("tools.settings") }}
            </div>
          </div>
        </div>
        <div :title="$t(tool.name)" class="tool" v-for="tool in toolList" :key="tool.id" Draggable="true" @dragstart="startToolDrag($event, tool)">
          <div class="flex_container tool_details">
            <div>
              <img :src="require(`@/assets/icons/${tool.icon}`)" class="tool_icon" />
            </div>
            <div class="tool_name">
              {{ $t(tool.name) }}
            </div>
          </div>
        </div>

        <div v-if="this.formLoaded">
          <div></div>
          <div v-if="formID == -1 && this.edited == false"></div>
          <div v-else>
            <b>{{ $t("status.form_status") }}:</b>
            <div>Form ID: {{ formID == -1 ? "---" : formID }}</div>
            <div v-if="lastPublished">{{ $t("status.last_published") }}: {{ lastPublished }}</div>
            <div v-else>{{ $t("status.last_published") }}: {{ $t("general.no") }}</div>
            <div>{{ $t("status.valid_form") }}: {{ this.formStatus == true ? $t("general.yes") : $t("general.no") }}</div>
            <div>{{ $t("status.enabled_in_store") }}: {{ this.formEnabledState == "Yes" ? $t("general.yes") : $t("general.no") }}</div>
          </div>
        </div>
        <div v-if="this.devMode">
          <div><b>Editing Status:</b></div>
          <div>Form Loaded: {{ this.formLoaded }}</div>
          <div>Edited: {{ this.edited }}</div>
          <div>Save Colour: {{ this.saveColour }}</div>
          <div>
            <pre>{{ formSettings }}</pre>
            <pre>{{ formItems }}</pre>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ConfirmContinue from "./ConfirmContinue.vue";
import LoadForm from "./LoadForm.vue";
import FormSettings from "./Tools/FormSettings.vue";
import Heading from "./Tools/Heading.vue";
import InfoText from "./Tools/InfoText.vue";
import InputField from "./Tools/InputField.vue";
import Dropdown from "./Tools/Dropdown.vue";
import DateField from "./Tools/DateField.vue";
import BooleanField from "./Tools/Boolean.vue";
import TextArea from "./Tools/TextArea.vue";
import CheckList from "./Tools/CheckList.vue";
import Mappings from "./Mappings.vue";
import Preview from "./Preview.vue";
import Axios from "axios";
import Draggable from "vuedraggable";

export default {
  name: "Forms",
  emits: ["saveElement", "closeTextBox"],
  components: {
    ConfirmContinue,
    LoadForm,
    Mappings,
    Preview,
    FormSettings,
    Heading,
    InfoText,
    InputField,
    Dropdown,
    DateField,
    BooleanField,
    TextArea,
    CheckList,
    Draggable,
  },
  data() {
    return {
      // Remmeber to check new tools in Dependancy component tool list
      toolList: [
        { id: 0, icon: "form-section.svg", name: "tools.section" },
        { id: 1, icon: "form-heading.svg", name: "tools.heading" },
        { id: 7, icon: "form-info-text.svg", name: "tools.info_text" },
        { id: 2, icon: "form-input.svg", name: "tools.inputbox" },
        { id: 3, icon: "form-dropdown.svg", name: "tools.dropdown" },
        { id: 4, icon: "form-date-time.svg", name: "tools.date_time" },
        { id: 5, icon: "form-boolean.svg", name: "tools.yes_no" },
        { id: 6, icon: "form-text-area.svg", name: "tools.text_area" },
        // { id: 8, icon: "form-check-list.svg", name: "tools.check_list" },
      ],
      // Set to true to view form details
      devMode: false,
      // New form defaults
      formDetail: {},
      formLoaded: false,
      formID: -1,
      formName: "",
      formDescription: "",
      //Default form settings
      defaultFormSettings: {
        labelPosition: "above",
        dateFormat: "Y-m-d",
        timeFormat: "24hr",
        themeFormat: "material_blue",
        columnGap: "10",
        rowGap: "0",
        sectionBackColour: "#ffffff",
        sectionTextColour: "#000000",
        sectionBorderColour: "#ffffff",
        sectionBorderSize: "0",
        sectionBorderRadius: "0",
        elementBackColour: "#ffffff",
        elementTextColour: "#000000",
        elementBorderColour: "#ffffff",
        elementBorderSize: "0",
        elementBorderRadius: "0",
      },
      formItems: [],
      formEnabled: false,
      formEnabledState: "",
      formNameError: false,
      formDescriptionError: false,
      formStatus: false,
      rowDetails: {},
      rowID: 0,
      formItemToEdit: 0,
      formSection: 0,
      formSaved: false,
      // Loaded form settings
      lastPublished: null,
      // General form settings
      origin: null,
      edited: false,
      saveColour: false,
      message: "",
      selectedformItemToEdit: null,
      confirmMessage: null,
      // Show modals
      showMappings: false,
      showPreview: false,
      showLoad: false,
      showConfirmContinue: false,
      // Tool modals
      showSettings: false,
      showHeading: false,
      showInfoText: false,
      showInputField: false,
      showDropdown: false,
      showDateField: false,
      showBooleanField: false,
      showTextArea: false,
      showDateRange: false,
      showDateTime: false,
      showCheckList: false,
      // Temp Move
      tempMoveId: null,
      // Api details
      url: process.env.VUE_APP_CM_STORE + "/api/rest.php",
      headers: { "Content-Type": "application/json" },
    };
  },
  mounted() {},
  created() {
    // Prevent background scroll
    document.body.style.overflow = "hidden";

    var postdata = {
      module: "cmsettings",
      action: "get_css",
      details: {},
    };
    Axios.post(this.url, postdata, this.headers).then((res) => {
      const data = JSON.stringify(res.data);
      //Stores CSS in local storage
      window.localStorage.setItem("css", data);
    });
  },
  computed: {
    dragOptions() {
      return {
        animation: 250,
        group: "description",
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
  methods: {
    getDependantName(tooItemID) {
      var dependantTitle = "";
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (columnDetail.cellDetails.toolItemID == tooItemID) {
            dependantTitle = columnDetail.cellDetails.toolDetails.title;
          }
        });
      });
      return this.displayTextByLength(dependantTitle);
    },
    displayLabelByLength(toolTypeID, text) {
      if (toolTypeID == 1) {
        if (text.length > 30) {
          return text.substring(0, 30) + " ....";
        }
        return text;
      }
      if (text.length > 30) {
        return 'Label: "' + text.substring(0, 30) + ' ...."';
      }
      if (text.length == 0) {
        return "Label: ";
      }
      return 'Label: "' + text + '"';
    },
    displaySubheadingByLength(text) {
      if (text) {
        if (text.length > 30) {
          return 'Subheading: "' + text.substring(0, 30) + ' ...."';
        }
        return 'Subheading: "' + text + '"';
      }
      return "";
    },
    displayTextByLength(text) {
      if (text.length > 30) {
        return text.substring(0, 30) + " ....";
      }
      return text;
    },
    getDependantComparison(comparison) {
      switch (comparison) {
        case "=":
          return "Equal to";
        case "!=":
          return "Not equal to";
        case "<":
          return "Less than";
        case ">":
          return "Greater than";
        case "inc":
          return "Includes";
        case "!inc":
          return "Does not include";
        case "Age >":
          return "Age greater than";
        case "Age >=":
          return "Age greater than or equal to";
        case "Age <":
          return "Age less than";
        case "Age <=":
          return "Age less than or equal to";
      }
    },
    getDependantCondition(parent) {
      if (parent.condition == true) {
        return '"Yes"';
      }
      if (parent.condition == false) {
        return '"No"';
      }
      var returnText = '"' + parent.condition + '"';
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (columnDetail.cellDetails.toolItemID == parent.parentField && columnDetail.cellDetails.toolTypeID == 3) {
            var options = columnDetail.cellDetails.toolDetails.dropDownOptions;
            var selectedOption = options.find((option) => option.value === parent.condition);
            if (selectedOption) {
              returnText = '"' + selectedOption.text + '"';
            }
          }
        });
      });

      return returnText;
    },
    newForm() {
      if (this.edited) {
        this.origin = "newForm";
        this.confirmMessage = "If you continue you will lose your changes. \nAre you sure you want to continue?";
        this.showConfirmContinue = true;
      } else {
        this.clearForm();
        this.formLoaded = true;
      }
    },
    clearForm() {
      this.formID = -1;
      this.formDetail = {};
      this.formItems = [];
      this.formName = "";
      this.formLoaded = false;
      this.saveColour = false;
      this.formEnabled = false;
      this.formStatus = false;
      this.formNameError = false;
      this.edited = false;
      this.lastPublished = null;
      this.lastPublishedStatus = null;
      this.formEnabledState = "No";
      this.message = "";
      this.formSettings = this.defaultFormSettings;
    },
    updateStatus(lastPublished, enabled) {
      this.lastPublished = lastPublished;
      this.formEnabled = enabled;
      if (this.formEnabled ? (this.formEnabledState = "Yes") : (this.formEnabledState = "No"));
    },
    openSettings() {
      this.showSettings = true;
    },
    startToolDrag(event, tool) {
      event.dataTransfer.setData("toolID", tool.id);
      event.dataTransfer.setData("type", "new");
    },
    startFormToolDrag(event, tool) {
      // When moving an exisiting tool
      event.dataTransfer.setData("toolID", tool.cellDetails.toolItemID);
      event.dataTransfer.setData("toolIDCurrent", event.currentTarget.id);
      event.dataTransfer.setData("type", "move");
      this.orginalDragId = event.currentTarget.id;
      this.orginalDragX = event.x;
      this.orginalDragY = event.y;
    },
    toolOnDrag(event) {
      var x = event.x;
      var y = event.y;
      var el = document.elementFromPoint(x, y);
      while (el.id == null || el.id == "") {
        el = el.parentElement;
      }
      var id = el.id;
      if (id.includes("cell")) {
        if (id == event.target.id) {
          if (this.tempMoveId != null) {
            document.getElementById(this.tempMoveId).classList.remove("tempHover");
          }
        } else {
          if (this.tempMoveId != null && id != this.tempMoveId) {
            document.getElementById(this.tempMoveId).classList.remove("tempHover");
          }
          document.getElementById(id).classList.add("tempHover");
          this.tempMoveId = id;
        }
      } else {
        if (this.tempMoveId != null) {
          document.getElementById(this.tempMoveId).classList.remove("tempHover");
        }
      }
    },
    onDropSection() {
      const toolID = event.dataTransfer.getData("toolID");
      if (toolID == "0") {
        this.addSection();
      }
    },
    onToolDrop(event) {
      const tool = event.dataTransfer.getData("toolID");
      const type = event.dataTransfer.getData("type");
      var newTarget = event.currentTarget.id;
      var originalTarget = event.dataTransfer.getData("toolIDCurrent");

      if (type == "new") {
        if (tool != "" && tool != "0") {
          this.addTool(tool, newTarget);
          this.setEdited();
        }
      }
      if (type == "move") {
        if (this.tempMoveId != null) {
          document.getElementById(this.tempMoveId).classList.remove("tempHover");
        }
        const posArray = newTarget.split("_");
        const posArrayOriginal = originalTarget.split("_");

        //Check for drop in same position as original
        if (posArray[1] != posArrayOriginal[1] || posArray[3] != posArrayOriginal[3]) {
          var toolBeingMovedRow;
          var toolBeingMovedCol;

          this.formItems.forEach((formItemMoved) => {
            // Find the new row
            if (formItemMoved.sectionRow == posArray[1]) {
              formItemMoved.columnDetails.forEach((columnDetailMove) => {
                // Find the new column
                if (columnDetailMove.sectionColumn == posArray[3]) {
                  // Copy the data across if the target is empty
                  var toolBeingMoved;

                  this.formItems.forEach((formItem) => {
                    var currentRow = formItem.sectionRow;
                    formItem.columnDetails.forEach((columnDetail) => {
                      // Get a copy of the tool being moved
                      if (columnDetail.cellDetails.toolItemID == tool) {
                        toolBeingMoved = Object.assign({}, columnDetail);
                        toolBeingMovedRow = currentRow;
                        toolBeingMovedCol = columnDetail.sectionColumn;
                        columnDetail.cellActive = false;
                        columnDetail.cellDetails = {};
                      }
                    });
                  });
                  if (columnDetailMove.cellActive == false) {
                    // The target is empty so we can move the tool

                    columnDetailMove.cellActive = true;
                    columnDetailMove.cellDetails = toolBeingMoved.cellDetails;
                    this.setEdited();
                  } else {
                    // swap
                    // The target is empty so we can move the tool
                    var toolBeingSwapped;
                    toolBeingSwapped = Object.assign({}, columnDetailMove);

                    columnDetailMove.cellActive = true;
                    columnDetailMove.cellDetails = toolBeingMoved.cellDetails;

                    this.formItems.forEach((formItemMoved) => {
                      if (formItemMoved.sectionRow == toolBeingMovedRow) {
                        formItemMoved.columnDetails.forEach((columnDetailMove) => {
                          // Get a copy of the tool being moved
                          if (columnDetailMove.sectionColumn == toolBeingMovedCol) {
                            columnDetailMove.cellActive = true;
                            columnDetailMove.cellDetails = toolBeingSwapped.cellDetails;
                          }
                        });
                      }
                    });

                    this.setEdited();
                  }
                }
              });
            }
          });
        }
      }
    },
    addSection() {
      if (this.formLoaded) {
        // Add a new section with value of 0. resetRows() will then find the correct value
        var newRow = { sectionRow: 0, columnDetails: [{ sectionColumn: 1, cellActive: false, cellDetails: {} }] };
        this.formItems.push(newRow);
        this.resetRows();
        this.setEdited();
        this.undoHighlights();
      }
    },
    editSection(formSectionID) {
      var formSection = this.formItems.find((sectionIndex) => sectionIndex.sectionRow == formSectionID);
      const sectionColumns = formSection.columnDetails.length;

      if (sectionColumns < 3) {
        var newRow = { sectionColumn: sectionColumns + 1, cellActive: false, cellDetails: {} };
        formSection.columnDetails.push(newRow);
      } else {
        this.message = "Max 3 columns";
      }
      this.setEdited();
      this.undoHighlights();
    },
    undoHighlights() {
      if (window.getSelection) {
        window.getSelection().removeAllRanges();
      } else if (document.selection) {
        document.selection.empty();
      }
    },
    deleteSection(target) {
      const posArray = target.split("_");
      var formSection = this.formItems.find((sectionIndex) => sectionIndex.sectionRow == posArray[1]);
      const noOfColumns = formSection.columnDetails.length;
      const columnToDelete = parseInt(posArray[3]);
      if (noOfColumns > 1) {
        const item = formSection.columnDetails.find((columnIndex) => columnIndex.sectionColumn == columnToDelete);

        formSection.columnDetails.splice(formSection.columnDetails.indexOf(item), 1);
      } else {
        this.formItems = this.formItems.filter((section) => section.sectionRow !== parseInt(posArray[1]));
      }
      this.resetRows();
      this.setEdited();
    },
    addTool(toolID, target) {
      // Check sectionis available for a tool and find the highest toolItemID
      var newToolItemID = 0;
      const targetCell = target.split("_");
      const targetRow = targetCell[1];
      const targetColumn = targetCell[3];
      var currentRow = 0;
      var currentColumn = 0;
      var active = false;

      this.formItems.forEach((formItem) => {
        currentRow = formItem.sectionRow;
        formItem.columnDetails.forEach((columnDetail) => {
          currentColumn = columnDetail.sectionColumn;
          if (currentRow == targetRow && currentColumn == targetColumn) {
            active = columnDetail.cellActive;
          }
          if (columnDetail.cellDetails.toolItemID > newToolItemID) {
            newToolItemID = columnDetail.cellDetails.toolItemID;
          }
        });
      });
      newToolItemID++;
      if (active == false) {
        const posArray = target.split("_");
        const tool = this.toolList.find((e) => e.id == toolID);
        const newToolItem = { toolItemID: newToolItemID, toolTypeID: tool.id, toolName: tool.name, toolDetails: { toolStatus: false, title: "" } };
        this.formStatus = false;
        var sectionRow = this.formItems.find((sectionRows) => sectionRows.sectionRow == posArray[1]);
        var sectionColumn = sectionRow.columnDetails.find((sectionColums) => sectionColums.sectionColumn == posArray[3]);
        sectionColumn.cellActive = true;
        sectionColumn.cellDetails = newToolItem;
        this.setEdited();
      }
    },
    deleteTool(toolItemID) {
      var hasDependants = false;
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (columnDetail.cellDetails.toolDetails && columnDetail.cellDetails.toolDetails.parent && columnDetail.cellDetails.toolDetails.parent.parentField == toolItemID) {
            hasDependants = true;
          }
        });
      });
      if (hasDependants) {
        this.origin = "info";
        this.confirmMessage = "You cannot delete this tool as it has dependants.\nPlease remove dependants first.";
        this.showConfirmContinue = true;
      } else {
        this.formItems.forEach((formItem) => {
          formItem.columnDetails.forEach((columnDetail) => {
            if (columnDetail.cellDetails.toolItemID == toolItemID) {
              columnDetail.cellActive = false;
              columnDetail.cellDetails = {};
            }
          });
        });
        this.setEdited();
      }
    },
    editTool(toolItemID) {
      this.message = "";
      if (toolItemID == "undefined") {
        //Bit of a hack to fix the background click issue
        // console.log("Forms.EditTool hack - need to fix");
        return false;
      }
      this.formItemToEdit = toolItemID;
      var selectedRow;

      // Find the tool in to the items to determine which tool editor to show
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (columnDetail.cellDetails.toolItemID == toolItemID) {
            selectedRow = columnDetail.cellDetails;
          }
        });
      });

      switch (selectedRow.toolTypeID) {
        case 1:
          this.showHeading = true;
          break;
        case 2:
          this.showInputField = true;
          break;
        case 3:
          this.showDropdown = true;
          break;
        case 4:
          this.showDateField = true;
          break;
        case 5:
          this.showBooleanField = true;
          break;
        case 6:
          this.showTextArea = true;
          break;
        case 7:
          this.showInfoText = true;
          break;
        case 8:
          this.showCheckList = true;
          break;
      }
    },
    setEdited() {
      this.edited = true;
      this.saveColour = true;
      this.message = "";
      this.formNameError = false;
      this.formSaved = false;

      this.checkFormStatus();
    },
    closeModal() {
      this.showLoad = false;
      this.showMappings = false;
      this.showPreview = false;
      this.showSettings = false;
      this.showHeading = false;
      this.showInfoText = false;
      this.showDropdown = false;
      this.showDateField = false;
      this.showBooleanField = false;
      this.showInputField = false;
      this.showTextArea = false;
      this.showDateRange = false;
      this.showDateTime = false;
      this.showCheckList = false;
      this.checkFormStatus();
    },
    closeContinue(e, o) {
      if (e == "Y" && o == "viewSavedForms") {
        this.showLoad = true;
      }
      if (e == "Y" && o == "newForm") {
        this.clearForm();
        this.formLoaded = true;
      }
      this.showConfirmContinue = false;
    },
    closeTool(toolItemID, toolStatus) {
      // Get the tool from the list of tools
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (columnDetail.cellDetails.toolItemID == toolItemID) {
            columnDetail.cellDetails.toolDetails.toolStatus = toolStatus;
          }
        });
      });
      this.closeModal();
    },
    saveSettings(formSettings) {
      this.formSettings = formSettings;

      // Set the date picker theme
      var stylesheet = document.head.querySelector("link[href*='themes']");
      stylesheet.href = "https://cdn.jsdelivr.net/npm/flatpickr/dist/themes/" + this.formSettings.themeFormat + ".css";

      this.edited = true;
      this.saveColour = true;
      this.showSettings = false;
      this.formSaved = false;
    },
    saveElement(element) {
      // Get the tool from the list of tools
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (columnDetail.cellDetails.toolItemID == element.toolItemID) {
            columnDetail.cellDetails.toolDetails = element.toolDetails;
          }
        });
      });
      this.closeModal();
      this.setEdited();
    },
    rowMoved() {
      this.setEdited();
      this.resetRows();
    },
    resetRows() {
      // Resets the row numbers
      var row = 0;
      this.formItems.forEach((formItem) => {
        row++;
        formItem.sectionRow = row;
      });
    },
    viewSavedForms() {
      if (this.edited) {
        this.origin = "viewSavedForms";
        this.confirmMessage = "If you continue you will lose your changes. \nAre you sure you want to continue?";
        this.showConfirmContinue = true;
      } else {
        //Set current form as not being edited
        // var postdata = {
        //   module: "forms",
        //   action: "form_being_edited",
        //   details: { formID: this.formID, beingEdited: "N" },
        // };

        // Axios.post(this.url, postdata, this.headers).then(() => {});
        this.showLoad = true;
      }
    },
    previewForm() {
      this.showPreview = true;
    },
    loadForm(formDetail) {
      this.formDetail = formDetail;
      this.message = "";
      this.formID = formDetail.formID;
      this.formName = formDetail.formName;
      if (formDetail.formSettings == null) {
        this.formSettings = this.defaultFormSettings;
      } else {
        this.formSettings = formDetail.formSettings;
      }
      var stylesheet = document.head.querySelector("link[href*='themes']");
      stylesheet.href = "https://cdn.jsdelivr.net/npm/flatpickr/dist/themes/" + this.formSettings.themeFormat + ".css";
      this.$i18n.locale = formDetail.formLanguage;
      this.lastPublished = formDetail.lastPublished;

      if (formDetail.formStatus == "1" ? (this.formStatus = true) : (this.formStatus = false));
      if (formDetail.formEnabled == "1" ? (this.formEnabledState = "Yes") : (this.formEnabledState = "No"));
      if (formDetail.formEnabled == "1" ? (this.formEnabled = true) : (this.formEnabled = false));

      const content = JSON.parse(formDetail.formContent);
      this.formItems = content;

      this.showLoad = false;
      this.edited = false;
      this.saveColour = false;
      this.formLoaded = true;
      this.formNameError = false;
      this.formSaved = true;

      //Set form as being edited
      var postdata = {
        module: "forms",
        action: "form_being_edited",
        details: { formID: this.formID, beingEdited: "Y" },
      };
      Axios.post(this.url, postdata, this.headers).then(() => {});
    },
    saveForm(e) {
      if (this.validateForm()) {
        var postdata = {
          module: "forms",
          details: { formID: this.formID, formDetails: this.formItems },
        };
        // Axios.post(this.url, postdata, this.headers).then((res) => {
        //   var postdata = {
        //     module: "forms",
        //     details: {},
        //   };
        if (this.formID == -1) {
          // This is a new form - add to the database
          postdata.action = "add_form";
        } else {
          postdata.details.formID = this.formID;
          // Save the form in its current state or publish it
          if (e == "save" ? (postdata.action = "update_form") : (postdata.action = "publish_form"));
        }

        postdata.details.formName = this.formName;
        postdata.details.formSettings = this.formSettings;
        postdata.details.formLanguage = this.$i18n.locale;
        postdata.details.formDescription = this.formDescription;
        postdata.details.formEnabled = this.formEnabled;
        postdata.details.formStatus = this.formStatus;
        postdata.details.formContent = this.formItems;

        Axios.post(this.url, postdata, this.headers).then((res) => {
          if (res.data == "form_name_exists") {
            this.message = "Form name has been used previously";
            this.formNameError = true;
          } else {
            if (e == "save") {
              this.message = "Form successfully saved";
            }
            if (e == "publish") {
              this.message = "Form successfully published";
            }
            this.edited = true;
            this.saveColour = false;
            this.edited = false;
            this.formID = res.data;
            this.formSaved = true;
          }
        });
      }
    },
    mappings() {
      this.showMappings = true;
    },
    checkFormStatus() {
      var statusCheckFlag = true;
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((column) => {
          if (column.cellActive) {
            if (column.cellDetails.toolDetails.toolStatus == false && statusCheckFlag == true) {
              statusCheckFlag = false;
            }
          }
        });
      });
      this.formStatus = statusCheckFlag;
    },
    validateForm() {
      this.formNameError = false;
      var valid = true;

      if (this.formName == "") {
        this.message = "Please give your form a name";
        this.formNameError = true;

        valid = false;
      }
      if (this.formItems.length == 0) {
        this.message = "Your form is empty.";
        valid = false;
      }
      if (valid == false) {
        return false;
      }
      return true;
    },
  },
};
</script>
<style scoped>
html {
  -webkit-tap-highlight-color: transparent;
}
.handle {
  padding: 5px;
  margin-right: 10px;
  border: solid #000 1px;
  cursor: move;
}
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: rgba(41, 91, 141, 0.2);
}
.default_value {
  margin-left: 20px;
}
.tempHover {
  transition: 0.5s ease all;
  opacity: 0.5;
  background: rgba(41, 91, 141, 0.2);
}
</style>
