<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">
        {{ $t("tools.datefield") }}
      </div>
      <div class="form_body">
        <div v-show="selectedDateOption != ''" class="form_section">
          <div class="form_preview_heading">{{ $t("tools.preview") }}</div>
          <div class="form_preview_detail">
            <div v-if="textBoxText">
              {{ textBoxText }}
            </div>
            <div v-else>{{ $t("tools.label") }}</div>
            <div v-show="selectedDateOption == 'Date'">
              <input type="text" v-model="previewDate" ref="previewDate" />
            </div>
            <div v-show="selectedDateOption == 'DateTime'">
              <input type="text" v-model="previewDateTime" ref="previewDateTime" />
            </div>
            <div v-show="selectedDateOption == 'Time'">
              <input type="text" v-model="previewTime" ref="previewTime" />
            </div>
            <div v-show="selectedDateOption == 'DateRange'">
              <input type="text" v-model="previewDateRange" ref="previewDateRange" />
            </div>
            <div v-show="selectedDateOption == 'DateRange2'" class="flex_container">
              From
              <input type="text" v-model="previewDateRange2Value1" ref="previewDateRange2Value1" @input="checkPreviewDate1()" />
              To
              <input type="text" v-model="previewDateRange2Value2" ref="previewDateRange2Value2" @input="checkPreviewDate2()" />
            </div>
            <div v-show="selectedDateOption == 'DateTimeRange'">
              <input type="text" v-model="previewDateTimeRange" ref="previewDateTimeRange" />
            </div>
            <div v-show="selectedDateOption == 'TimeRange'" class="flex_container">
              From
              <input type="text" v-model="previewTimeRangeValue1" ref="previewTimeRangeValue1" @input="checkPreviewTime1()" />
              To
              <input type="text" v-model="previewTimeRangeValue2" ref="previewTimeRangeValue2" @input="checkPreviewTime2()" />
            </div>
          </div>
        </div>

        <TargetField :target="target" :formItem="formItem" :formItems="formItems" :targetSelectError="targetSelectError" @updateTarget="updateTarget" ref="targetRef" />

        <div v-show="selectedDateOption != ''" class="form_section">
          <div class="form_section_heading">{{ $t("tools.label") }}</div>
          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.label_text") }}</label>
            <b-form-input v-model="textBoxText" :placeholder="$t('place_holders.label_text')" :class="{ 'input-error': textBoxTextError }"></b-form-input>
          </div>

          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.tool_tip") }}</label>
            <b-form-input v-model="toolTipText" :placeholder="$t('place_holders.tool_tip')"></b-form-input>
          </div>
        </div>

        <div v-show="selectedDateOption != ''" class="form_section">
          <div class="form_section_heading">{{ $t("tools.validation") }}</div>
          <!-- Validation for date and date time -->
          <div
            v-show="selectedDateOption == 'Date' || selectedDateOption == 'DateTime' || selectedDateOption == 'DateRange' || selectedDateOption == 'DateRange2'"
            class="form_section_detail"
          >
            <div class="flex_container">
              <div class="flex_item">
                <label :for="valMinDate">Minimum date</label><br />
                <input type="text" v-model="valMinDate" ref="valMinDate" @input="checkMinDate()" />
              </div>
              <div class="flex_item">
                <label :for="valMaxDate">Maximum date</label><br />
                <input type="text" v-model="valMaxDate" ref="valMaxDate" @input="checkMaxDate()" />
              </div>
            </div>
            <div v-show="false" class="flex_container">
              <div class="flex_item">
                <label :for="valMinDateError">Minimum date error message</label><br />
                <b-form-input v-model="valMinDateError" type="text" @input="checkMinErrorMessge($event)"></b-form-input>
              </div>
              <div class="flex_item">
                <label :for="valMaxDateError">Maximum date error message</label><br />
                <b-form-input v-model="valMaxDateError" type="text" @input="checkMaxErrorMessge($event)"></b-form-input>
              </div>
            </div>
          </div>

          <!-- Validation for time -->
          <div v-show="selectedDateOption == 'Time' || selectedDateOption == 'TimeRange'" class="form_section_detail">
            <div class="flex_container">
              <div class="flex_item">
                <label :for="valMinTime">Minimum time</label><br />
                <input type="text" v-model="valMinTime" ref="valMinTime" @input="checkMinTime()" />
              </div>
              <div class="flex_item">
                <label :for="valMaxTime">Maximum time</label><br />
                <input type="text" v-model="valMaxTime" ref="valMaxTime" @input="checkMaxTime()" />
              </div>
            </div>
            <div v-show="false" class="flex_container">
              <div class="flex_item">
                <label :for="valMinTimeError">Minimum time error message</label><br />
                <b-form-input v-model="valMinTimeError" type="text" @input="checkMinTimeErrorMessge($event)"></b-form-input>
              </div>
              <div class="flex_item">
                <label :for="valMaxTimeError">Maximum time error message</label><br />
                <b-form-input v-model="valMaxTimeError" type="text" @input="checkMaxTimeErrorMessge($event)"></b-form-input>
              </div>
            </div>
          </div>
        </div>

        <div v-show="selectedDateOption != ''" class="form_section">
          <div class="form_section_heading">Default</div>
          <div
            v-show="selectedDateOption == 'Date' || selectedDateOption == 'DateTime' || selectedDateOption == 'DateRange' || selectedDateOption == 'DateRange2'"
            class="form_section_detail"
          >
            <div class="flex_container">
              <div v-show="selectedDateOption == 'Date'">
                <input type="text" v-model="defaultDateValue1" ref="defaultDate" @input="updateDefaultValue()" />
              </div>
              <div v-show="selectedDateOption == 'DateRange'">
                <input type="text" v-model="defaultDateValue1" ref="defaultDateRange" @input="updateDefaultValue()" />
              </div>
              <div v-show="selectedDateOption == 'DateRange2'">
                <div class="flex_container">
                  From
                  <input type="text" v-model="defaultDateValue1" ref="defaultDateRangev2_1" @input="updateDefaultValue()" />
                  To
                  <input type="text" v-model="defaultDateValue2" ref="defaultDateRangev2_2" @input="updateDefaultValue()" />
                </div>
              </div>
              <div v-if="defaultValue != '' && defaultValue != null" class="flex_item flex_item_button">
                <b-button class="form_button_inline" @click="clearDefault()">Clear</b-button>
              </div>
            </div>
          </div>

          <div v-show="selectedDateOption == 'Time' || selectedDateOption == 'TimeRange'" class="form_section_detail">
            <div class="flex_container">
              <div class="flex_item">
                <div v-show="selectedDateOption == 'Time'">
                  <input type="text" v-model="defaultTime" ref="defaultTime" @input="updateDefaultValue()" />
                </div>
                <div v-show="selectedDateOption == 'TimeRange'" class="flex_container">
                  From
                  <input type="text" v-model="defaultTimeRange1" ref="defaultTimeRange1" @input="updateDefaultValue()" />
                  To
                  <input type="text" v-model="defaultTimeRange2" ref="defaultTimeRange2" @input="updateDefaultValue()" />
                </div>
              </div>
              <!-- <div class="flex_item">
                <div v-if="defaultValue != '' && defaultValue != null" class="flex_item flex_item_button">
                  <b-button class="form_button" @click="clearDefault()">Clear</b-button>
                </div>
              </div> -->
            </div>
          </div>
        </div>

        <Dependency
          v-show="selectedDateOption != ''"
          :dependency="dependency"
          :formItem="formItem"
          :formItems="formItems"
          :formSettings="formSettings"
          @updateDependency="updateDependency"
          ref="dependencyRef"
        />
      </div>
      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">{{ $t("buttons.cancel") }}</b-button>
        <b-button class="form_button" @click="saveElement()">{{ $t("buttons.save") }}</b-button>
        <div>{{ message }}</div>
      </div>
    </div>
  </div>
</template>
<script>
import "./Tool.css";
import Dependency from "./Dependency.vue";
import TargetField from "./TargetField.vue";
import { ref } from "vue";
import flatpickr from "flatpickr";

export default {
  name: "TextboxTool",
  components: { Dependency, TargetField },
  emits: ["dependency", "target", "saveElement", "closeTool"],
  props: ["formItemToEdit", "formItems", "formSettings"],
  data() {
    return {
      dateOptions: [
        { value: "Date", text: "Date" },
        { value: "DateTime", text: "Date & Time" },
        { value: "Time", text: "Time" },
        { value: "DateRange", text: "Date range" },
        { value: "DateRange2", text: "Date range 2" },
        // { value: "DateTimeRange", text: "Date time range" },
        { value: "TimeRange", text: "Time range" },
      ],
      dateOptionsFiltered: [],
      toolList: [{ value: "", text: "Please select ..." }],
      allRows: {},
      toolListToView: false,
      rowItem: this.rowToEdit,
      dependencyRef: ref(),
      targetRef: ref(),
      dependency: {},
      target: {},
      textBoxText: "",
      toolTipText: "",
      message: "",
      selectedDateOption: "",
      toolStatus: null,
      textBoxTextError: false,
      targetSelectError: false,
      dateFormat: "Y-m-d",
      time_24hr: false,
      timeFormat: "h:i K",

      //default values
      previewDate: "",
      previewDateTime: "",
      previewTime: "",
      previewDateRange: "",
      previewDateTimeRange: "",
      previewDateRange2Value1: "",
      previewDateRange2Value2: "",
      previewTimeRangeValue1: "",
      previewTimeRangeValue2: "",

      valMinDate: "",
      valMaxDate: "",
      valMinDateError: "Date too early",
      valMaxDateError: "Date too late",
      valMinTime: "01:00",
      valMaxTime: "22:55",
      valMinTimeError: "Time too early",
      valMaxTimeError: "Time too late",

      defaultDateValue1: "",
      defaultDateValue2: "",
      defaultTime: "",
      defaultTimeRange1: "",
      defaultTimeRange2: "",
      defaultRangeValue: "",

      defaultLowerLimit: "",
      defaultUpperLimit: "",

      defaultValue: "",
    };
  },
  mounted() {
    new flatpickr(this.$refs.previewDate, { altInput: true, altFormat: this.dateFormat });
    new flatpickr(this.$refs.previewDateTime, { altInput: true, altFormat: this.dateFormat + " H:i", enableTime: true });
    new flatpickr(this.$refs.previewDateRange, { altInput: true, altFormat: this.dateFormat, mode: "range" });
    new flatpickr(this.$refs.previewDateRange2Value1, { altInput: true, altFormat: this.dateFormat, minDate: this.valMinDate, maxDate: this.valMaxDate });
    new flatpickr(this.$refs.previewDateRange2Value2, { altInput: true, altFormat: this.dateFormat, minDate: this.valMinDate, maxDate: this.valMaxDate });
    new flatpickr(this.$refs.previewDateTimeRange, { altInput: true, altFormat: this.dateFormat + " H:i", mode: "range", time_24hr: this.time_24hr, enableTime: true });
    new flatpickr(this.$refs.valMinDate, { altInput: true, altFormat: this.dateFormat });
    new flatpickr(this.$refs.valMaxDate, { altInput: true, altFormat: this.dateFormat });
    new flatpickr(this.$refs.defaultDate, { altInput: true, altFormat: this.dateFormat });
    new flatpickr(this.$refs.defaultDateRange, { mode: "range", altInput: true, altFormat: this.dateFormat });
    new flatpickr(this.$refs.defaultDateRangev2_1, { altInput: true, altFormat: this.dateFormat });
    new flatpickr(this.$refs.defaultDateRangev2_2, { altInput: true, altFormat: this.dateFormat });

    new flatpickr(this.$refs.previewTime, {
      altInput: true,
      altFormat: this.timeFormat,
      enableTime: true,
      noCalendar: true,
      time_24hr: this.time_24hr,
      minTime: this.valMinTime,
      maxTime: this.valMaxTime,
      dateFormat: "H:i",
    });
    new flatpickr(this.$refs.previewTimeRangeValue1, {
      altInput: true,
      enableTime: true,
      altFormat: this.timeFormat,
      time_24hr: this.time_24hr,
      noCalendar: true,
      minTime: this.valMinTime,
      maxTime: this.valMaxTime,
    });
    new flatpickr(this.$refs.previewTimeRangeValue2, {
      altInput: true,
      enableTime: true,
      altFormat: this.timeFormat,
      time_24hr: this.time_24hr,
      noCalendar: true,
      minTime: this.valMinTime,
      maxTime: this.valMaxTime,
    });
    new flatpickr(this.$refs.valMinTime, {
      altInput: true,
      enableTime: true,
      altFormat: this.timeFormat,
      noCalendar: true,
      minTime: "01:00",
      maxTime: this.defaultLowerLimit,
      time_24hr: this.time_24hr,
    });
    new flatpickr(this.$refs.valMaxTime, {
      altInput: true,
      enableTime: true,
      altFormat: this.timeFormat,
      noCalendar: true,
      minTime: this.defaultUpperLimit,
      maxTime: "22:30",
      time_24hr: this.time_24hr,
    });
    new flatpickr(this.$refs.defaultTime, {
      altInput: true,
      enableTime: true,
      altFormat: this.timeFormat,
      time_24hr: this.time_24hr,
      noCalendar: true,
      minTime: this.defaultLowerLimit,
      maxTime: this.defaultUpperLimit,
    });
    new flatpickr(this.$refs.defaultTimeRange1, {
      altInput: true,
      enableTime: true,
      altFormat: this.timeFormat,
      time_24hr: this.time_24hr,
      noCalendar: true,
      minTime: this.valMinTime,
      maxTime: this.defaultUpperLimit,
    });
    new flatpickr(this.$refs.defaultTimeRange2, {
      altInput: true,
      enableTime: true,
      altFormat: this.timeFormat,
      time_24hr: this.time_24hr,
      noCalendar: true,
      minTime: this.defaultLowerLimit,
      maxTime: this.valMaxTime,
    });
  },
  created() {
    this.dateOptionsFiltered = this.dateOptions;
    //Get the tool from the list of tools
    this.formItems.forEach((formItem) => {
      formItem.columnDetails.forEach((columnDetail) => {
        if (columnDetail.cellDetails.toolItemID == this.formItemToEdit) {
          this.formItem = columnDetail.cellDetails;
        }
      });
    });
    //Load the saved tool details
    // Label and tool tip
    this.textBoxText = this.formItem.toolDetails.title;
    if (this.formItem.toolDetails.toolTipText) {
      this.toolTipText = this.formItem.toolDetails.toolTipText;
    }
    //Parent details
    if (this.formItem.toolDetails.parent) {
      this.dependency.parent = this.formItem.toolDetails.parent;
    }
    //Target details
    if (this.formItem.toolDetails.target) {
      this.target = this.formItem.toolDetails.target;
    }
    //Date options details
    if (this.formItem.toolDetails.dateOptions) {
      this.selectedDateOption = this.formItem.toolDetails.dateOptions;
      const x = this.selectedDateOption;
      this.dateOptionsFiltered = this.dateOptions.filter(function (e) {
        return e.value == x;
      });
    }
    //Default details
    if (this.formItem.toolDetails.default) {
      this.defaultValue = this.formItem.toolDetails.default;
      this.setDefaults();
    }
    //Validation details
    if (this.formItem.toolDetails.validation) {
      switch (this.selectedDateOption) {
        case "Date":
        case "DateRange":
        case "DateRange2":
          this.valMinDate = this.formItem.toolDetails.validation.minDateTime;
          this.valMaxDate = this.formItem.toolDetails.validation.maxDateTime;
          this.valMinDateError = this.formItem.toolDetails.validation.minDateTimeError;
          this.valMaxDateError = this.formItem.toolDetails.validation.maxDateTimeError;
          break;
        case "Time":
        case "TimeRange":
          this.valMinTime = this.formItem.toolDetails.validation.minDateTime;
          this.valMaxTime = this.formItem.toolDetails.validation.maxDateTime;
          this.valMinTimeError = this.formItem.toolDetails.validation.minDateTimeError;
          this.valMaxTimeError = this.formItem.toolDetails.validation.maxDateTimeError;
          this.defaultLowerLimit = this.defaultValue;
          this.defaultUpperLimit = this.defaultValue;
          if (this.selectedDateOption == "TimeRange") {
            var dateTimeArray = this.defaultValue.split(" to ");
            this.defaultLowerLimit = dateTimeArray[0];
            this.defaultUpperLimit = dateTimeArray[1];
          }
          break;
        default:
          break;
      }
    }
    //Toolstatus
    if (this.formItem.toolDetails.toolStatus) {
      this.toolStatus = this.formItem.toolDetails.toolStatus;
    }
    //FormSettings
    if (this.formSettings) {
      if (this.formSettings.dateFormat) {
        this.dateFormat = this.formSettings.dateFormat;
      }
      if (this.formSettings.timeFormat) {
        if (this.formSettings.timeFormat == "24hr") {
          this.time_24hr = true;
          this.timeFormat = "H:i";
        } else {
          this.time_24hr = false;
          this.timeFormat = this.formSettings.timeFormat;
        }
      }
    }
  },
  methods: {
    setMinMax() {
      this.$refs.previewDate._flatpickr.set("minDate", this.valMinDate);
      this.$refs.previewDate._flatpickr.set("maxDate", this.valMaxDate);

      // this.$refs.valMinTime._flatpickr.set("minTime", this.valMinTime);
      // this.$refs.valMaxTime._flatpickr.set("maxTime", this.valMaxTime);

      this.$refs.previewDateRange2Value1._flatpickr.set("minDate", this.valMinDate);
      this.$refs.previewDateRange2Value2._flatpickr.set("minDate", this.valMinDate);
      this.$refs.previewDateRange2Value1._flatpickr.set("maxDate", this.valMaxDate);
      this.$refs.previewDateRange2Value2._flatpickr.set("maxDate", this.valMaxDate);
    },
    checkMinDate() {
      if (this.defaultValue != null && this.defaultValue != "") {
        var lowerLimit = this.defaultValue;
        // var upperLimit = this.defaultValue;
        var altLower = "";
        var altUpper = "";
        if (this.selectedDateOption == "DateRange" || this.selectedDateOption == "DateRange2") {
          var dateTimeArray = this.defaultValue.split(" to ");
          lowerLimit = dateTimeArray[0];
          // upperLimit = dateTimeArray[1];
          altLower = this.$refs.defaultDateRangev2_1._flatpickr.altInput.value;
          altUpper = this.$refs.defaultDateRangev2_2._flatpickr.altInput.value;
        }
        if (this.selectedDateOption == "Date") {
          altLower = this.$refs.defaultDate._flatpickr.altInput.value;
          altUpper = this.$refs.defaultDate._flatpickr.altInput.value;
        }

        if (this.valMinDate > lowerLimit) {
          this.valMinDate = lowerLimit;
          this.$refs.valMinDate._flatpickr.altInput.value = altLower;
        }
        if (this.valMaxDate < lowerLimit) {
          this.valMaxDate = lowerLimit;
          this.$refs.valMaxDate._flatpickr.altInput.value = altUpper;
        }
      }

      if (this.valMinDate > this.valMaxDate) {
        this.valMaxDate = this.valMinDate;
        this.$refs.valMaxDate._flatpickr.altInput.value = this.$refs.valMinDate._flatpickr.altInput.value;
      }
      this.setMinMax();
      this.setErrorMessages();
    },
    checkMaxDate() {
      if (this.defaultValue != null && this.defaultValue != "") {
        var upperLimit = this.defaultValue;
        var altLower = "";
        var altUpper = "";
        if (this.selectedDateOption == "DateRange" || this.selectedDateOption == "DateRange2") {
          var dateTimeArray = this.defaultValue.split(" to ");
          upperLimit = dateTimeArray[1];
          altLower = this.$refs.defaultDateRangev2_1._flatpickr.altInput.value;
          altUpper = this.$refs.defaultDateRangev2_2._flatpickr.altInput.value;
        }
        if (this.selectedDateOption == "Date") {
          altLower = this.$refs.defaultDate._flatpickr.altInput.value;
          altUpper = this.$refs.defaultDate._flatpickr.altInput.value;
        }
        if (this.valMinDate > upperLimit) {
          this.valMinDate = upperLimit;
          this.$refs.valMaxDate._flatpickr.altInput.value = altLower;
        }
        if (this.valMaxDate < upperLimit) {
          this.valMaxDate = upperLimit;
          this.$refs.valMaxDate._flatpickr.altInput.value = altUpper;
        }
      }
      if (this.valMinDate > this.valMaxDate) {
        this.valMinDate = this.valMaxDate;
        this.$refs.valMinDate._flatpickr.altInput.value = this.$refs.valMaxDate._flatpickr.altInput.value;
      }
      this.setMinMax();
      this.setErrorMessages();
    },
    checkMinTime() {
      if (this.defaultValue != null && this.defaultValue != "") {
        var lowerLimit = this.defaultValue;
        var altLower = "";
        var altUpper = "";
        if (this.selectedDateOption == "TimeRange") {
          var dateTimeArray = this.defaultValue.split(" to ");
          lowerLimit = dateTimeArray[0];
          altLower = this.$refs.defaultTimeRange1._flatpickr.altInput.value;
          altUpper = this.$refs.defaultTimeRange2._flatpickr.altInput.value;
        }
        if (this.selectedDateOption == "Time") {
          altLower = this.$refs.defaultTime._flatpickr.altInput.value;
          altUpper = this.$refs.defaultTime._flatpickr.altInput.value;
        }
        if (this.valMinTime > lowerLimit) {
          this.valMinTime = lowerLimit;
          this.$refs.valMinTime._flatpickr.altInput.value = altLower;
        }
        if (this.valMaxTime < lowerLimit) {
          this.valMaxTime = lowerLimit;
          this.$refs.valMaxTime._flatpickr.altInput.value = altUpper;
        }

        // if (this.valMinTime > lowerLimit) {
        //   this.valMinTime = lowerLimit;
        //   this.$refs.valMinTime.value = lowerLimit;
        //   this.$refs.valMinTime.maxTime = lowerLimit;
        //   this.$refs.valMinTime._flatpickr.altInput.value = lowerLimit;
        // }
      }

      if (this.valMinTime > this.valMaxTime) {
        this.valMaxTime = this.valMinTime;
        this.$refs.valMaxTime._flatpickr.altInput.value = this.$refs.valMinTime._flatpickr.altInput.value;
      }

      this.setMinMax();
      this.setErrorMessages();
    },
    checkMaxTime() {
      if (this.defaultValue != null && this.defaultValue != "") {
        var upperLimit = this.defaultValue;
        if (this.selectedDateOption == "TimeRange") {
          var dateTimeArray = this.defaultValue.split(" to ");
          upperLimit = dateTimeArray[1];
        }
        if (this.valMaxTime < upperLimit) {
          this.valMaxTime = upperLimit;
          this.$refs.valMinTime.value = upperLimit;
          this.$refs.valMaxTime._flatpickr.set("minTime", upperLimit);
          this.$refs.valMaxTime.minTime = upperLimit;
          this.$refs.valMaxTime._flatpickr.altInput.value = upperLimit;
        }
      }
      if (this.valMinTime > this.valMaxTime) {
        this.valMinTime = this.valMaxTime;
        this.$refs.valMinTime._flatpickr.altInput.value = this.$refs.valMaxTime._flatpickr.altInput.value;
      }

      this.setMinMax();
      this.setErrorMessages();
    },
    updateDefaultValue() {
      switch (this.selectedDateOption) {
        case "Date":
          this.previewDate = this.$refs.defaultDate.value;
          this.$refs.previewDate._flatpickr.altInput.value = this.$refs.defaultDate._flatpickr.altInput.value;
          this.defaultValue = this.$refs.defaultDate.value;

          if (this.$refs.defaultDate.value < this.valMinDate && this.valMinDate != "") {
            this.valMinDate = this.$refs.defaultDate.value;
            this.$refs.valMinDate._flatpickr.altInput.value = this.$refs.defaultDate._flatpickr.altInput.value;
          }
          if (this.$refs.defaultDate.value > this.valMaxDate && this.valMaxDate != "") {
            this.valMaxDate = this.$refs.defaultDate.value;
            this.$refs.valMaxDate._flatpickr.altInput.value = this.$refs.defaultDate._flatpickr.altInput.value;
          }
          this.setMinMax();

          break;
        case "DateRange":
          this.previewDateRange = this.$refs.defaultDateRange.value;
          this.$refs.previewDateRange.value = this.$refs.defaultDateRange.value;
          this.defaultValue = this.$refs.defaultDateRange.value;
          break;
        case "DateRange2":
          // Update date if other is null
          if (this.defaultDateValue1 == "") {
            this.defaultDateValue1 = this.defaultDateValue2;
            this.$refs.defaultDateRangev2_1._flatpickr.altInput.value = this.$refs.defaultDateRangev2_2._flatpickr.altInput.value;
          }
          if (this.defaultDateValue2 == "") {
            this.defaultDateValue2 = this.defaultDateValue1;
            this.$refs.defaultDateRangev2_2._flatpickr.altInput.value = this.$refs.defaultDateRangev2_1._flatpickr.altInput.value;
          }
          // Update date date1 > date2
          if (this.defaultDateValue1 > this.defaultDateValue2) {
            this.defaultDateValue2 = this.defaultDateValue1;
            this.$refs.defaultDateRangev2_2._flatpickr.altInput.value = this.$refs.defaultDateRangev2_1._flatpickr.altInput.value;
          }
          // Update min and max if defaults outside min max range
          if (this.valMinDate > this.defaultDateValue1 || this.valMinDate == "") {
            this.valMinDate = this.defaultDateValue1;
            this.$refs.valMinDate._flatpickr.altInput.value = this.$refs.defaultDateRangev2_1._flatpickr.altInput.value;
          }
          if (this.valMaxDate < this.defaultDateValue2 || this.valMaxDate == "") {
            this.valMaxDate = this.defaultDateValue2;
            this.$refs.valMaxDate._flatpickr.altInput.value = this.$refs.defaultDateRangev2_2._flatpickr.altInput.value;
          }

          this.setMinMax();

          // Update preview dates
          this.previewDateRange2Value1 = this.defaultDateValue1;
          this.previewDateRange2Value2 = this.defaultDateValue2;
          this.$refs.previewDateRange2Value1._flatpickr.altInput.value = this.$refs.defaultDateRangev2_1._flatpickr.altInput.value;
          this.$refs.previewDateRange2Value2._flatpickr.altInput.value = this.$refs.defaultDateRangev2_2._flatpickr.altInput.value;

          this.defaultValue = this.defaultDateValue1 + " to " + this.defaultDateValue2;

          this.setErrorMessages();
          break;
        case "Time":
          this.previewTime = this.$refs.defaultTime.value;
          this.$refs.previewTime._flatpickr.altInput.value = this.$refs.defaultTime._flatpickr.altInput.value;
          this.defaultValue = this.$refs.defaultTime.value;

          this.$refs.valMinTime._flatpickr.set("maxTime", this.$refs.defaultTime.value);
          this.$refs.valMaxTime._flatpickr.set("minTime", this.$refs.defaultTime.value);

          if (this.$refs.defaultTime.value < this.valMinTime && this.valMinTime != "") {
            this.valMinTime = this.$refs.defaultTime.value;
            this.$refs.valMinTime._flatpickr.altInput.value = this.$refs.defaultTime._flatpickr.altInput.value;
          }
          if (this.$refs.defaultTime.value > this.valMaxTime && this.valMaxTime != "") {
            this.valMaxTime = this.$refs.defaultTime.value;
            this.$refs.valMaxTime._flatpickr.altInput.value = this.$refs.defaultTime._flatpickr.altInput.value;
          }

          this.setMinMax();

          break;
        case "TimeRange":
          // Update date if other is null
          if (this.defaultTimeRange1 == "") {
            this.defaultTimeRange1 = this.defaultTimeRange2;
            this.$refs.defaultTimeRange1._flatpickr.altInput.value = this.$refs.defaultTimeRange2._flatpickr.altInput.value;
          }
          if (this.defaultTimeRange2 == "") {
            this.defaultTimeRange2 = this.defaultTimeRange1;
            this.$refs.defaultTimeRange2._flatpickr.altInput.value = this.$refs.defaultTimeRange1._flatpickr.altInput.value;
          }
          // Update date date1 > date2
          if (this.defaultTimeRange1 > this.defaultTimeRange2) {
            this.defaultTimeRange2 = this.defaultTimeRange1;
            this.$refs.defaultTimeRange2._flatpickr.altInput.value = this.$refs.defaultTimeRange1._flatpickr.altInput.value;
          }

          this.$refs.valMinTime._flatpickr.set("maxTime", this.defaultTimeRange1);
          this.$refs.valMaxTime._flatpickr.set("minTime", this.defaultTimeRange2);

          this.$refs.defaultTimeRange1._flatpickr.set("maxTime", this.defaultTimeRange2);
          this.$refs.defaultTimeRange2._flatpickr.set("minTime", this.defaultTimeRange1);

          // Update min and max if defaults outside min max range
          if (this.valMinTime > this.defaultTimeRange1) {
            this.valMinTime = this.defaultTimeRange1;
            this.$refs.valMinTime._flatpickr.altInput.value = this.$refs.defaultTimeRange1._flatpickr.altInput.value;
          }
          if (this.valMaxTime < this.defaultTimeRange2) {
            this.valMaxTime = this.defaultTimeRange2;
            this.$refs.valMaxTime._flatpickr.altInput.value = this.$refs.defaultTimeRange2._flatpickr.altInput.value;
          }
          // Update preview dates
          this.previewTimeRangeValue1 = this.defaultTimeRange1;
          this.previewTimeRangeValue2 = this.defaultTimeRange2;
          this.$refs.previewTimeRangeValue1._flatpickr.altInput.value = this.$refs.defaultTimeRange1._flatpickr.altInput.value;
          this.$refs.previewTimeRangeValue2._flatpickr.altInput.value = this.$refs.defaultTimeRange2._flatpickr.altInput.value;

          this.defaultValue = this.defaultTimeRange1 + " to " + this.defaultTimeRange2;

          this.setErrorMessages();
          break;
      }
    },
    checkPreviewDate1() {
      if (this.previewDateRange2Value2 == "") {
        this.previewDateRange2Value2 = this.previewDateRange2Value1;
        this.$refs.previewDateRange2Value2._flatpickr.altInput.value = this.$refs.previewDateRange2Value1._flatpickr.altInput.value;
      }
      if (this.previewDateRange2Value1 > this.previewDateRange2Value2) {
        this.previewDateRange2Value1 = this.previewDateRange2Value2;
        this.$refs.previewDateRange2Value1._flatpickr.altInput.value = this.$refs.previewDateRange2Value2._flatpickr.altInput.value;
      }
      this.checkPreviewDate();
    },
    checkPreviewDate2() {
      if (this.previewDateRange2Value1 == "") {
        this.previewDateRange2Value1 = this.previewDateRange2Value2;
        this.$refs.previewDateRange2Value1._flatpickr.altInput.value = this.$refs.previewDateRange2Value2._flatpickr.altInput.value;
      }
      if (this.previewDateRange2Value2 < this.previewDateRange2Value1) {
        this.previewDateRange2Value2 = this.previewDateRange2Value1;
        this.$refs.previewDateRange2Value2._flatpickr.altInput.value = this.$refs.previewDateRange2Value1._flatpickr.altInput.value;
      }
      this.checkPreviewDate();
    },
    checkPreviewDate() {
      //Checks against min and max
      if (this.valMaxDate != "" && this.previewDateRange2Value1 > this.valMaxDate) {
        this.$refs.previewDateRange2Value1._flatpickr.altInput.value = this.$refs.valMaxDate._flatpickr.altInput.value;
      }
      if (this.valMaxDate != "" && this.previewDateRange2Value2 > this.valMaxDate) {
        this.$refs.previewDateRange2Value2._flatpickr.altInput.value = this.$refs.valMaxDate._flatpickr.altInput.value;
      }
      if (this.valMinDate != "" && this.previewDateRange2Value1 < this.valMinDate) {
        this.$refs.previewDateRange2Value1._flatpickr.altInput.value = this.$refs.valMinDate._flatpickr.altInput.value;
      }
      if (this.valMinDate != "" && this.previewDateRange2Value2 < this.valMinDate) {
        this.$refs.previewDateRange2Value2._flatpickr.altInput.value = this.$refs.valMinDate._flatpickr.altInput.value;
      }
    },
    checkPreviewTime1() {
      if (this.previewTimeRangeValue1 > this.previewTimeRangeValue2 || this.previewTimeRangeValue2 == "") {
        this.previewTimeRangeValue2 = this.previewTimeRangeValue1;
      }
      this.checkPreviewTime();
    },
    checkPreviewTime2() {
      if (this.previewTimeRangeValue2 < this.previewTimeRangeValue1 || this.previewTimeRangeValue1 == "") {
        this.previewTimeRangeValue1 = this.previewTimeRangeValue2;
      }
      this.checkPreviewTime();
    },
    checkPreviewTime() {
      //Checks against min and max
      if (this.previewTimeRangeValue1 < this.valMinTime) {
        this.$refs.previewTimeRangeValue1._flatpickr.altInput.value = this.$refs.minTime._flatpickr.altInput.value;
        this.previewTimeRangeValue1 = this.valMinTime;
      }
      if (this.previewTimeRangeValue2 > this.valMaxTime) {
        this.$refs.previewTimeRangeValue2._flatpickr.altInput.value = this.$refs.maxTime._flatpickr.altInput.value;
        this.previewTimeRangeValue2 = this.valMaxTime;
      }
    },

    updateDependency(dependency) {
      this.dependency.parent = dependency;
    },
    updateTarget(target) {
      this.resetDefault();
      this.dateOptionsFiltered = this.dateOptions.filter(function (e) {
        return e.value == target.fieldType;
      });
      this.selectedDateOption = target.fieldType;
      this.target = target;
    },
    setDefaults() {
      switch (this.selectedDateOption) {
        case "Date":
          this.defaultDateValue1 = this.defaultValue;
          break;
        case "DateRange":
          this.defaultDateValue1 = this.defaultValue;
          break;
        case "DateRange2":
          var dateArray = this.defaultValue.split(" to ");
          this.previewDateRange2Value1 = dateArray[0];
          this.previewDateRange2Value2 = dateArray[1];
          this.defaultDateValue1 = dateArray[0];
          this.defaultDateValue2 = dateArray[1];
          break;
        case "Time":
          this.defaultTime = this.defaultValue;
          break;
        case "TimeRange":
          var timeArray = this.defaultValue.split(" to ");
          this.previewTimeRangeValue1 = timeArray[0];
          this.previewTimeRangeValue2 = timeArray[1];
          this.defaultTimeRange1 = timeArray[0];
          this.defaultTimeRange2 = timeArray[1];
          break;
        default:
          break;
      }
    },

    resetDefault() {
      this.textBoxText = "";

      this.previewDate = "";
      this.previewDateTime = "";
      this.previewTime = "";
      this.previewDateRange = "";
      this.previewDateRange2Value1 = "";
      this.previewDateRange2Value2 = "";
      this.previewDateTimeRange = "";
      this.previewTimeRangeValue1 = "";
      this.previewTimeRangeValue2 = "";
      this.valMinDate = "";
      this.valMaxDate = "";
      this.valMinTime = "";
      this.valMaxTime = "";

      this.defaultValue = "";

      this.defaultDateValue1 = "";
      this.defaultDateValue2 = "";
      this.defaultTimeRange1 = "";
      this.defaultTimeRange2 = "";
      this.defaultRangeValue = "";

      this.$refs.previewTimeRangeValue1._flatpickr.altInput.value = "";
      this.$refs.previewTimeRangeValue2._flatpickr.altInput.value = "";
      this.$refs.defaultDateRange._flatpickr.altInput.value = "";
      this.$refs.defaultDateRangev2_1._flatpickr.altInput.value = "";
      this.$refs.defaultDateRangev2_2._flatpickr.altInput.value = "";
      this.$refs.defaultTimeRange1._flatpickr.altInput.value = "";
      this.$refs.defaultTimeRange2._flatpickr.altInput.value = "";
      this.$refs.valMinDate._flatpickr.altInput.value = "";
      this.$refs.valMaxDate._flatpickr.altInput.value = "";
      this.$refs.valMinTime._flatpickr.altInput.value = "";
      this.$refs.valMaxTime._flatpickr.altInput.value = "";
      this.$refs.defaultDate._flatpickr.altInput.value = "";

      this.valMinDateError = "Date too early";
      this.valMaxDateError = "Date too late";
      this.valMinTimeError = "Time too early";
      this.valMaxTimeError = "Time too late";
    },
    setErrorMessages() {
      switch (this.selectedDateOption) {
        case "Date":
          this.valMinDateError = "Date must be on or after " + this.$refs.valMinDate._flatpickr.altInput.value;
          this.valMaxDateError = "Date must be on or before " + this.$refs.valMaxDate._flatpickr.altInput.value;
          break;
        case "DateRange":
          this.valMinTimeError = "Date must be between " + this.$refs.valMinDate._flatpickr.altInput.value + " & " + this.$refs.valMaxDate._flatpickr.altInput.value;
          this.valMaxDateError = "Date must be between " + this.$refs.valMinDate._flatpickr.altInput.value + " & " + this.$refs.valMaxDate._flatpickr.altInput.value;
          break;
        case "DateRange2":
          this.valMinDateError = "Date must be between " + this.$refs.valMinDate._flatpickr.altInput.value + " & " + this.$refs.valMaxDate._flatpickr.altInput.value;
          this.valMaxDateError = "Date must be between " + this.$refs.valMinDate._flatpickr.altInput.value + " & " + this.$refs.valMaxDate._flatpickr.altInput.value;
          break;
        case "Time":
          this.valMinTimeError = "Time must be on or after " + this.$refs.valMinTime._flatpickr.altInput.value;
          this.valMaxTimeError = "Time must be on or before " + this.$refs.valMaxTime._flatpickr.altInput.value;
          break;
        case "TimeRange":
          this.valMinTimeError = "Time must be between " + this.$refs.valMinTime._flatpickr.altInput.value + " & " + this.$refs.valMaxTime._flatpickr.altInput.value;
          this.valMaxTimeError = "Time must be between " + this.$refs.valMinTime._flatpickr.altInput.value + " & " + this.$refs.valMaxTime._flatpickr.altInput.value;
          break;
        default:
          break;
      }
    },
    checkMinErrorMessge(e) {
      if (e == "") {
        this.valMinDateError = "Date too early";
      }
    },
    checkMaxErrorMessge(e) {
      if (e == "") {
        this.valMaxDateError = "Date too late";
      }
    },
    checkMinTimeErrorMessge(e) {
      if (e == "") {
        this.valMinTimeError = "Time too early";
      }
    },
    checkMaxTimeErrorMessge(e) {
      if (e == "") {
        this.valMaxTimeError = "Time too late";
      }
    },
    checkDefaultValue(e) {
      const selected = e.srcElement.value;
      if (this.valMinDate != null && selected < this.valMinDate) {
        this.valMinDate = selected;
      }
      if (this.valMaxDate != null && selected > this.valMaxDate) {
        this.valMaxDate = selected;
      }
      this.previewDate = selected;
      this.setErrorMessages();
    },
    clearDefault() {
      this.defaultDate = "";
      this.defaultDateValue1 = "";
      this.defaultDateValue2 = "";
      this.defaultTimeRange1 = "";
      this.defaultTimeRange2 = "";
      this.defaultRangeValue = "";

      this.previewDateRange2Value1 = "";
      this.previewDateRange2Value2 = "";

      this.$refs.defaultDate._flatpickr.altInput.value = "";

      this.$refs.previewDateRange2Value1._flatpickr.altInput.value = "";
      this.$refs.previewDateRange2Value2._flatpickr.altInput.value = "";

      this.$refs.defaultDateRangev2_1._flatpickr.altInput.value = "";
      this.$refs.defaultDateRangev2_2._flatpickr.altInput.value = "";

      this.$refs.defaultTimeRange1._flatpickr.altInput.value = "";
      this.$refs.defaultTimeRange2._flatpickr.altInput.value = "";

      this.defaultValue = "";
    },
    closeElement() {
      // this.rowItem = this.rowToEdit;
      this.$emit("closeTool", this.formItem.toolItemID, this.formItem.toolDetails.toolStatus);
    },
    saveElement() {
      if (this.validate()) {
        this.message = "";
        this.formItem.toolDetails.title = this.textBoxText;
        this.formItem.toolDetails.toolTipText = this.toolTipText;

        if (this.dependency.parent !== undefined) {
          if (this.dependency.parent.parentField === "") {
            delete this.formItem.toolDetails.parent;
          } else {
            this.formItem.toolDetails.parent = this.dependency.parent;
          }
        }
        var validation = {};
        switch (this.selectedDateOption) {
          case "Date":
          case "DateRange":
          case "DateRange2":
            validation = {
              minDateTime: this.valMinDate,
              maxDateTime: this.valMaxDate,
              minDateTimeError: this.valMinDateError,
              maxDateTimeError: this.valMaxDateError,
            };
            break;
          case "Time":
          case "TimeRange":
            validation = {
              minDateTime: this.valMinTime,
              maxDateTime: this.valMaxTime,
              minDateTimeError: this.valMinTimeError,
              maxDateTimeError: this.valMaxTimeError,
            };
            break;
        }
        this.formItem.toolDetails.validation = validation;
        this.formItem.toolDetails.dateOptions = this.selectedDateOption;
        this.formItem.toolDetails.target = this.target;
        this.formItem.toolDetails.toolStatus = this.toolStatus;
        this.formItem.toolDetails.default = this.defaultValue;
        this.$emit("saveElement", this.formItem);
      }
    },
    validate() {
      var valid = true;
      this.toolStatus = false;
      this.textBoxTextError = false;
      this.targetSelectError = false;

      if (this.textBoxText == undefined || this.textBoxText == "") {
        this.textBoxTextError = true;
        valid = false;
      }
      if (this.target.targetField == undefined) {
        this.targetSelectError = true;
        valid = false;
      }
      // Validate the dependency
      if (this.$refs.dependencyRef.validate() == false) {
        valid = false;
      }
      if (valid == false) {
        return false;
      }

      this.toolStatus = true;
      return true;
    },
  },
};
</script>
<style>
.table > thead {
  top: 0px;
  background: white;
  position: sticky;
  z-index: 99;
}
</style>
