<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">
        {{ $t("tools.dropdown") }}
      </div>
      <div class="form_body">
        <div class="form_section">
          <div class="form_preview_heading">{{ $t("tools.preview") }}</div>
          <div class="form_preview_detail">
            <div class="flex_container">
              <div class="flex_item">
                <div v-if="dropdownText">
                  {{ dropdownText }}
                </div>

                <div v-else>{{ $t("tools.label") }}</div>
                <b-form-select v-model="selected" :options="dropDownOptions" multiple></b-form-select>
              </div>
            </div>
          </div>
        </div>

        <TargetField :target="target" :formItem="formItem" :formItems="formItems" :targetSelectError="targetSelectError" @updateTarget="updateTarget" ref="tagretRef" />

        <div v-show="target.targetField != ''" class="form_section">
          <div class="form_section_heading">{{ $t("tools.label") }}</div>
          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.label_text") }}</label>
            <b-form-input v-model="dropdownText" placeholder="Text" :class="{ 'input-error': dropdownTextError }"></b-form-input>
          </div>
          <div class="form_section_detail">
            <label :for="formItem.toolDetails.title">{{ $t("tools.tool_tip") }}</label>
            <b-form-input v-model="toolTipText" :placeholder="$t('place_holders.tool_tip')"></b-form-input>
          </div>
        </div>

        <div v-show="target.targetField != ''" class="form_section">
          <div class="form_section_heading">Options</div>
          <div class="form_section_detail">
            {{ $t("dropdown.options_title") }}
            <div class="flex_container">
              <div class="flex_item">
                <div class="flex_container_column">
                  <div class="flex_item">
                    {{ $t("general.label") }}
                    <b-form-input
                      v-model="optionLabel"
                      :placeholder="$t('place_holders.option_label')"
                      @keyup.enter="addOption()"
                      :class="{ 'input-error': optionLabelDuplicate }"
                      ref="labelInput"
                    ></b-form-input>
                  </div>
                  <div class="flex_item">
                    {{ $t("general.value") }}
                    <b-form-input
                      v-model="optionValue"
                      :placeholder="$t('place_holders.option_value')"
                      @keyup.enter="addOptionValue()"
                      :class="{ 'input-error': optionValueDuplicate }"
                    ></b-form-input>
                  </div>
                  <div class="flex_item">
                    <!-- <button v-show="optionEdited != null && optionValue != '' && optionLabel != ''" class="option_edit_btn" @click="addOption()">{{ $t("buttons.add") }}</button> -->
                    <!-- <button v-show="optionEdited" class="option_edit_btn" @click="updateOption()">{{ $t("buttons.update") }}</button> -->
                    <button v-show="optionEdited" class="option_edit_btn" @click="clearOption()">{{ $t("buttons.clear") }}</button>
                  </div>
                </div>
              </div>

              <div class="flex_item">
                {{ $t("dropdown.options_list_title") }}
                <Draggable :list="dropDownOptions" item-key="text" handle=".section_handle_dropdown" class="option_dropdown_list list-group">
                  <template #item="{ element }">
                    <div v-show="element.value != ''" class="option-list" @click="editOption({ element })">
                      <div class="flex_container">
                        <div class="section_handle_dropdown">
                          <b>::</b>
                          <!-- <img src="@/assets/icons/form-up-down.svg" class="section_handle_icon" /> -->
                        </div>
                        <div class="option_choice">
                          {{ element.text.length > 23 ? element.text.substring(0, 20) + "..." : element.text }} ({{
                            element.value.length > 23 ? element.value.substring(0, 20) + "..." : element.value
                          }})
                        </div>
                      </div>

                      <div>
                        <button class="option_list_btn" @click.stop="deleteOption(element.text)">{{ $t("buttons.delete") }}</button>
                      </div>
                    </div>
                  </template>
                </Draggable>
              </div>
            </div>
            <div class="mulitple_select">
              <div class="mulitple_select_label">{{ $t("dropdown.multiple_select") }}</div>
              <b-form-checkbox id="bs_override" v-model="multipleSelect"> </b-form-checkbox>
            </div>
          </div>
        </div>

        <div v-show="target.targetField != ''" class="form_section">
          <div class="form_section_heading">{{ $t("general.default") }}</div>
          <div class="form_section_detail">
            <div>
              <label :for="defaultValue">{{ $t("general.default_value") }}</label>
              <b-form-select v-model="defaultValue" :options="dropDownOptions" @change="setDefault($event)"></b-form-select>
            </div>
          </div>
        </div>

        <Dependency
          v-show="target.targetField != ''"
          :dependency="dependency"
          :formItem="formItem"
          :formItems="formItems"
          :formSettings="formSettings"
          @updateDependency="updateDependency"
          ref="dependencyRef"
        />
      </div>
      <div>{{ message }}</div>

      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">{{ $t("buttons.cancel") }}</b-button>
        <b-button class="form_button" @click="saveElement()">{{ $t("buttons.save") }}</b-button>
      </div>
    </div>
  </div>
  <div v-if="showConfirmContinue">
    <ConfirmContinue :origin="origin" :confirmMessage="confirmMessage" @closeContinue="closeContinue" />
  </div>
</template>
<script>
import "./Tool.css";
import ConfirmContinue from "../ConfirmContinue.vue";
import Dependency from "./Dependency.vue";
import TargetField from "./TargetField.vue";
import Draggable from "vuedraggable";
import { ref } from "vue";

export default {
  name: "HeadingTool",
  components: { ConfirmContinue, Dependency, TargetField, Draggable },
  emits: ["dependency", "target", "saveElement", "closeTool"],
  props: ["formItemToEdit", "formItems", "formSettings"],
  data() {
    return {
      dropDownOptions: [{ value: "", text: "Please select ..." }],
      dropdownText: "",
      optionValue: "",
      optionLabel: "",
      labelInput: ref(),
      optionLabelDuplicate: false,
      optionValueDuplicate: false,
      optionEdited: null,
      checkOptionLabel: null,
      checkOptionValue: null,
      multipleSelect: false,
      selected: "",
      dependency: {},
      parent: {},
      target: { targetField: "" },
      toolTipText: "",
      message: null,
      dropdownTextError: false,
      targetSelectError: false,
      toolStatus: false,
      defaultValue: "",
      showConfirmContinue: false,
    };
  },
  created() {
    //Get the tool from the list of tools
    this.formItems.forEach((formItem) => {
      formItem.columnDetails.forEach((columnDetail) => {
        if (columnDetail.cellDetails.toolItemID == this.formItemToEdit) {
          this.formItem = columnDetail.cellDetails;
        }
      });
    });
    //Load the saved tool details
    // Label and tool tip
    this.dropdownText = this.formItem.toolDetails.title;
    if (this.formItem.toolDetails.toolTipText) {
      this.toolTipText = this.formItem.toolDetails.toolTipText;
    }
    // Add dropdown options if they exist, otherwise it will be a blank array
    if (this.formItem.toolDetails.dropDownOptions) {
      this.dropDownOptions = this.formItem.toolDetails.dropDownOptions;
    }
    if (this.formItem.toolDetails.multipleSelect) {
      this.multipleSelect = this.formItem.toolDetails.multipleSelect;
    }
    //Parent details
    if (this.formItem.toolDetails.parent) {
      this.dependency.parent = this.formItem.toolDetails.parent;
    }
    //Target details
    if (this.formItem.toolDetails.target) {
      this.target = this.formItem.toolDetails.target;
    }
    //Default details
    if (this.formItem.toolDetails.default) {
      this.defaultValue = this.formItem.toolDetails.default;
    }
    //Validation details
    if (this.formItem.toolDetails.validation) {
      this.validation = this.formItem.toolDetails.validation;
    }
    //Toolstatus
    if (this.formItem.toolDetails.toolStatus) {
      this.toolStatus = this.formItem.toolDetails.toolStatus;
    }
  },
  methods: {
    addOption() {
      if (this.optionEdited != null) {
        this.updateOption();
      } else {
        this.setOptionlValue();
        const dupilcate = this.checkDuplicates();
        if (this.optionValue != "" && this.optionLabel != "" && dupilcate == false) {
          const newOption = { value: this.optionValue, text: this.optionLabel }; //Note value and label is currently the same
          this.dropDownOptions.push(newOption);
          this.optionValue = "";
          this.optionLabel = "";
          this.optionEdited = null;
          this.formItem.toolDetails.dropDownOptions = this.dropDownOptions;
        }
      }
    },
    addOptionValue() {
      if (this.optionEdited != null) {
        this.updateOption();
      } else {
        const dupilcate = this.checkDuplicates();
        if (this.optionValue != "" && this.optionLabel != "" && dupilcate == false) {
          const newOption = { value: this.optionValue, text: this.optionLabel }; //Note value and label is currently the same
          this.dropDownOptions.push(newOption);
          this.optionValue = "";
          this.optionLabel = "";
          this.optionEdited = null;
          this.formItem.toolDetails.dropDownOptions = this.dropDownOptions;
        }
        if (this.optionLabel == "") {
          this.$refs.labelInput.focus();
        }
      }
    },
    checkDuplicates() {
      this.optionLabelDuplicate = false;
      this.optionValueDuplicate = false;

      this.dropDownOptions.forEach((dropDownOption) => {
        if (dropDownOption.text.toLowerCase() == this.optionLabel.toLowerCase()) {
          if (this.optionEdited == null) {
            this.optionLabelDuplicate = true;
          } else {
            if (this.optionEdited.text != this.optionLabel.toLowerCase()) {
              this.optionLabelDuplicate = true;
            }
          }
        }
        if (dropDownOption.value.toLowerCase() == this.optionValue.toLowerCase()) {
          if (this.optionEdited == null) {
            this.optionValueDuplicate = true;
          } else {
            if (this.optionEdited.value != this.optionValue.toLowerCase()) {
              this.optionValueDuplicate = true;
            }
          }
        }
      });

      this.checkOptionLabel = this.dropDownOptions.find((rowIndex) => rowIndex.text == this.optionLabel);
      this.checkOptionValue = this.dropDownOptions.find((rowIndex) => rowIndex.value == this.optionValue);

      if (this.checkOptionLabel || this.optionValueDuplicate) {
        // Duplicate found
        return true;
      }

      return false;
    },
    editOption(e) {
      this.optionEdited = this.dropDownOptions.find((rowIndex) => rowIndex.text == e.element.text);
      this.optionValue = e.element.value;
      this.optionLabel = e.element.text;
    },

    updateOption() {
      const dupilcate = this.checkDuplicates();
      if (this.optionValue != "" && this.optionLabel != "" && dupilcate == false) {
        this.updateDependants(this.optionEdited.text);

        var updatedOption = this.dropDownOptions.find((rowIndex) => rowIndex.text == this.optionEdited.text);
        updatedOption.value = this.optionValue;
        updatedOption.text = this.optionLabel;
        this.optionValue = "";
        this.optionLabel = "";
        this.optionEdited = null;
      }
    },
    clearOption() {
      this.optionLabelDuplicate = false;
      this.optionValueDuplicate = false;
      this.optionValue = "";
      this.optionLabel = "";
      this.optionEdited = null;
    },
    setOptionlValue() {
      this.optionLabelDuplicate = false;
      this.optionValueDuplicate = false;
      if (this.optionEdited == null && this.optionValue == "") {
        this.optionValue = this.optionLabel.toLowerCase();
        this.optionValue = this.optionLabel.replaceAll(" ", "_");
      }
    },
    updateDependency(dependency) {
      this.dependency.parent = dependency;
    },
    updateTarget(target) {
      this.target = target;
    },
    setDefault(e) {
      if (e == "") {
        delete this.formItem.toolDetails.default;
      } else {
        this.formItem.toolDetails.default = e;
      }
    },
    closeContinue() {
      this.showConfirmContinue = false;
    },
    closeElement() {
      this.$emit("closeTool", this.formItem.toolItemID, this.formItem.toolDetails.toolStatus);
    },
    deleteOption(option) {
      var hasDependants = this.checkDependants(option);

      if (hasDependants == false) {
        const item = this.dropDownOptions.find((rowIndex) => rowIndex.text == option);
        if (item.value == this.defaultValue) {
          this.defaultValue = "";
        }
        this.dropDownOptions.splice(this.dropDownOptions.indexOf(item), 1);
        this.optionValue = "";
        this.optionLabel = "";
        this.optionLabelDuplicate = false;
        this.optionValueDuplicate = false;
      }
    },
    updateDependants(e) {
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (this.formItem.toolItemID != columnDetail.cellDetails.toolItemID) {
            if (
              columnDetail.cellDetails.toolDetails &&
              columnDetail.cellDetails.toolDetails.parent &&
              columnDetail.cellDetails.toolDetails.parent.parentField == this.formItem.toolItemID &&
              columnDetail.cellDetails.toolDetails.parent.condition == e
            ) {
              columnDetail.cellDetails.toolDetails.parent.condition = this.optionLabel;
            }
          }
        });
      });
    },
    checkDependants(e) {
      var hasDependants = false;
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          if (this.formItem.toolItemID != columnDetail.cellDetails.toolItemID) {
            if (columnDetail.cellDetails.toolDetails && columnDetail.cellDetails.toolDetails.parent && columnDetail.cellDetails.toolDetails.parent.condition == e) {
              hasDependants = true;
            }
          }
        });
      });
      if (hasDependants) {
        this.origin = "info";
        this.confirmMessage = "You cannot edit this option another field in your form is dependant upon it.\nPlease remove dependants first.";
        this.showConfirmContinue = true;
      }
      return hasDependants;
    },
    saveElement() {
      if (this.validate()) {
        this.formItem.toolDetails.title = this.dropdownText;
        this.formItem.toolDetails.toolTipText = this.toolTipText;
        this.formItem.toolDetails.dropDownOptions = this.dropDownOptions;
        this.formItem.toolDetails.multipleSelect = this.multipleSelect;
        this.formItem.toolDetails.parent = this.dependency.parent;
        this.formItem.toolDetails.target = this.target;
        this.formItem.toolDetails.toolStatus = this.toolStatus;
        this.$emit("saveElement", this.formItem);
      }
    },
    validate() {
      var valid = true;
      this.dropdownTextError = false;

      this.toolStatus = false;
      if (this.dropdownText == undefined || this.dropdownText == "") {
        this.dropdownTextError = true;
        valid = false;
      }
      if (this.target.targetField == undefined) {
        this.targetSelectError = true;
        valid = false;
      }
      // Validate the dependency
      if (this.$refs.dependencyRef.validate() == false) {
        valid = false;
      }
      if (valid == false) {
        return false;
      }
      this.toolStatus = true;
      return true;
    },
  },
};
</script>
<style>
.option_button {
  width: 100px;
}
.option-list {
  display: flex;
  justify-content: space-between;
  height: 30px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  padding: 2px;
  margin-top: 3px;
  font-size: small;
}
.option_list_btn {
  padding: 1px;
  background-color: #6c757d;
  color: white;
  border: 1px solid #6c757d;
  border-radius: 3px;
  font-size: small;
}
.option_edit_btn {
  padding: 1px;
  background-color: #6c757d;
  color: white;
  border: 1px solid #6c757d;
  border-radius: 3px;
  font-size: small;
  width: 100px;
  margin-right: 10px;
}
.option_choice {
  margin: 3px 0px 0px 5px;
}
.mulitple_select {
  display: flex;
}
.mulitple_select_label {
  margin-right: 10px;
}
</style>
