<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">{{ $t("load_forms.saved_forms") }}</div>
      <div class="form_body">
        <div v-if="loading">{{ $t(loadingText) }}</div>
        <div v-if="!loading">
          <b-table striped hover v-bind:sort-by="sortBy" :sort-desc="sortDesc" :items="forms" :fields="fields" @row-clicked="loadForm">
            <template #cell(formEnabled)="data">
              <div v-if="data.item.formEnabled == '1'" class="-align-left">Yes</div>
              <div v-else>No</div>
            </template>

            <template #cell(duplicate)="row">
              <b-button size="sm" @click="duplicateForm(row)" class="table_button"> Duplicate </b-button>
            </template>
            <template #cell(delete)="row">
              <b-button size="sm" @click="deleteForm(row)" class="table_button"> Delete </b-button>
            </template>
          </b-table>
        </div>
      </div>

      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">Close</b-button>
      </div>
      <div v-if="showConfirmContinue">
        <ConfirmContinue :origin="origin" :confirmMessage="confirmMessage" @closeContinue="closeContinue" />
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import ConfirmContinue from "./ConfirmContinue.vue";
// import i18n from "./../i18n";

export default {
  name: "LoadForm",
  components: { ConfirmContinue },
  data() {
    return {
      forms: [],
      url: process.env.VUE_APP_CM_STORE + "/api/rest.php",
      headers: { "Content-Type": "application/json" },
      postdata: {
        module: "forms",
        details: {},
      },
      fields: [
        // { key: "formID", label: this.translateCol("status.form_id"), sortable: true },
        { key: "formID", label: "Form ID", sortable: true },
        { key: "formName", label: "Form Name", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "formEnabled", label: "Enabled", sortable: true },
        { key: "lastPublished", label: "Last Published", sortable: true },
        { key: "delete", label: "Delete", sortable: false },
        { key: "duplicate", label: "Duplicate", sortable: false },
      ],

      loading: true,
      loadingText: "general.loading",
      showConfirmContinue: false,
      sortBy: "formName",
      sortDesc: "Asc",
      confirmType: null,
      formToAction: -1,
      confirmMessage: null,
    };
  },
  created() {
    //Get all forms
    this.getAllForms();
  },

  methods: {
    getAllForms() {
      this.postdata.action = "all_forms";
      Axios.post(this.url, this.postdata, this.headers).then((res) => {
        res.data.forEach((value) => {
          const formRow = {
            formID: value.form_id,
            formName: value.form_name,
            formReadOnly: value.form_read_only,
            formSettings: JSON.parse(value.form_settings),
            formLanguage: value.form_language,
            formDescription: value.form_description,
            beingEdited: value.being_edited,
            formEnabled: value.form_enabled,
            formStatus: value.form_status,
            formContent: value.form_content,
            lastPublished: value.last_published,
          };
          this.forms.push(formRow);
        });
        this.loading = false;
      });
    },
    translateCol(colName) {
      return this.$i18n.t(colName);
    },
    loadForm(formRow) {
      if (formRow.beingEdited == "YX") {
        //Stops the form being loaded if being edited
      } else {
        if (!this.confirmMessage) {
          const formDetail = this.forms.find((form) => form.formID == formRow.formID);
          if (formDetail.formReadOnly == "Y") {
            if (!this.confirmMessage) {
              const formDetail = this.forms.find((form) => form.formID == formRow.formID);
              if (formDetail.formReadOnly == "Y") {
                this.formToAction = formRow.formID;
                this.origin = "loadForms";
                this.confirmMessage = "This form is read only. Do you want to duplicate this form?";
                this.confirmType = "duplicate";
                this.showConfirmContinue = true;
              } else {
                this.$emit("loadForm", formDetail);
              }
            }
          } else {
            this.$emit("loadForm", formDetail);
          }
        }
      }
    },
    duplicateForm(formRow) {
      this.formToAction = formRow.item.formID;
      this.origin = "loadForms";
      this.confirmMessage = "Are you sure you want to duplicate this form?";
      this.confirmType = "duplicate";
      this.showConfirmContinue = true;
    },
    deleteForm(formRow) {
      this.formToAction = formRow.item.formID;
      this.origin = "loadForms";
      this.confirmMessage = "Are you sure you want to delete this form?";
      this.confirmType = "delete";
      this.showConfirmContinue = true;
    },
    closeContinue(e) {
      this.showConfirmContinue = false;
      if (e == "Y") {
        this.formActionConfirmed();
      }
      this.confirmMessage = null;
    },
    formActionConfirmed() {
      switch (this.confirmType) {
        case "duplicate":
          this.postdata.action = "duplicate_form";
          break;
        case "delete":
          this.postdata.action = "delete_form";
          break;
      }
      this.postdata.details = { formID: this.formToAction };
      Axios.post(this.url, this.postdata, this.headers).then(() => {
        if (this.confirmType == "duplicate") {
          this.forms = [];
          this.getAllForms();
        }
        if (this.confirmType == "delete") {
          const index = this.forms.find((rowIndex) => rowIndex.formID == this.formToAction);
          this.forms.splice(this.forms.indexOf(index), 1);
          this.$emit("formDeleted");
        }
      });
    },
    closeElement() {
      this.$emit("closeModal");
    },
  },
};
</script>

<style scoped>
.table_button {
  width: 100px;
}
</style>
