<template>
  <div>
    <div>
      <Navbar />
    </div>
    <div>
      <router-view />
    </div>
  </div>
</template>
<script>
import Navbar from "./components/Navbar/Navbar.vue";
import Axios from "axios";

export default {
  name: "App",
  components: {
    Navbar,
  },
  data() {
    return {
      //Api details
      url: process.env.VUE_APP_CM_STORE + "/api/rest.php",
      headers: { "Content-Type": "application/json" },
    };
  },
  created() {
    var postdata = {
      module: "authorise",
      action: "authorise",
      details: {},
    };
    Axios.post(this.url, postdata, this.headers).then((res) => {
      if (res.data == "Failed") {
        console.log("Login failed");
        alert("User is not logged in to store admin - in future access will be redirected to storeadmin");
        //window.location.assign(process.env.VUE_APP_CM_STORE + "/storeadmin");
      } else {
        console.log("Login success");
      }
    });
  },
};
</script>
<style>
#app {
  display: inline important;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
