<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">
        {{ $t("tools.heading") }}
      </div>
      <div class="form_body">
        <div class="form_section">
          <div class="form_section_heading">{{ $t("tools.heading") }}</div>
          <div class="form_section_detail">
            {{ $t("tools.heading_text") }}
            <b-form-input v-model="title" :placeholder="$t('place_holders.heading_text')" :class="{ 'input-error': textBoxTextError }"></b-form-input>
          </div>
        </div>
        <div class="form_section">
          <div class="form_section_heading">{{ $t("tools.heading") }}</div>
          <div class="form_section_detail flex_container">
            <div class="flex_item">
              {{ $t("tools.heading_alignment") }}
              <b-form-select v-model="headingAlignment" :options="headingAlignmentList"> </b-form-select>
            </div>
            <!-- <div class="flex_item">
              {{ $t("tools.heading_size") }}
              <b-form-select v-model="headingSize" :options="headingSizeList"> </b-form-select>
            </div> -->
          </div>
        </div>
        <div class="form_section">
          <div class="form_section_heading">{{ $t("tools.sub_heading") }}</div>
          <div class="form_section_detail">
            {{ $t("tools.sub_heading_text") }}
            <b-form-input v-model="subheading" :placeholder="$t('place_holders.subheading_text')" :class="{ 'input-error': textBoxTextError }"></b-form-input>
          </div>
        </div>
        <Dependency :dependency="dependency" :formItem="formItem" :formItems="formItems" :formSettings="formSettings" @updateDependency="updateDependency" ref="dependencyRef" />
      </div>
      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">{{ $t("buttons.cancel") }}</b-button>
        <b-button class="form_button" @click="saveElement()">{{ $t("buttons.save") }}</b-button>
      </div>
    </div>
  </div>
</template>
<script>
import "./Tool.css";
import Dependency from "./Dependency.vue";

export default {
  name: "HeadingTool",
  components: { Dependency },

  props: ["formItemToEdit", "formItems", "closeTool"],
  data() {
    return {
      headingText: "",
      headingAlignmentList: [
        { value: "left", text: "Left" },
        { value: "center", text: "Centre" },
        { value: "right", text: "Right" },
      ],
      headingSizeList: [
        { value: "H1", text: "H1" },
        { value: "H2", text: "H2" },
        { value: "H3", text: "H3" },
        { value: "H4", text: "H4" },
      ],
      dependency: {},
      headingAlignment: "center",
      headingSize: "H4",
      formItem: {},
      toolStatus: false,
      textBoxTextError: false,
      subheading: "",
    };
  },
  created() {
    //Get the tool from the list of tools
    this.formItems.forEach((formItem) => {
      formItem.columnDetails.forEach((columnDetail) => {
        if (columnDetail.cellDetails.toolItemID == this.formItemToEdit) {
          this.formItem = columnDetail.cellDetails;
        }
      });
    });
    //Heading
    this.title = this.formItem.toolDetails.title;

    //Heading settings
    if (this.formItem.toolDetails.headingAlignment) {
      this.headingAlignment = this.formItem.toolDetails.headingAlignment;
    }
    if (this.formItem.toolDetails.headingSize) {
      this.headingSize = this.formItem.toolDetails.headingSize;
    }
    //Subheading
    if (this.formItem.toolDetails.subheading) {
      this.subheading = this.formItem.toolDetails.subheading;
    }
    //Parent details
    if (this.formItem.toolDetails.parent) {
      this.dependency.parent = this.formItem.toolDetails.parent;
    }
    //Toolstatus
    if (this.formItem.toolDetails.toolStatus) {
      this.toolStatus = this.formItem.toolDetails.toolStatus;
    }
  },
  methods: {
    closeElement() {
      this.$emit("closeTool", this.formItem.toolItemID, this.formItem.toolDetails.toolStatus);
    },
    saveElement() {
      if (this.validate()) {
        this.formItem.toolDetails.title = this.title;
        this.formItem.toolDetails.subheading = this.subheading;
        this.formItem.toolDetails.headingAlignment = this.headingAlignment;
        this.formItem.toolDetails.parent = this.dependency.parent;
        this.formItem.toolDetails.headingSize = this.headingSize;
        this.formItem.toolDetails.toolStatus = this.toolStatus;

        this.$emit("saveElement", this.formItem);
      }
    },
    updateDependency(dependency) {
      this.dependency.parent = dependency;
    },
    validate() {
      var valid = true;

      // Validate the dependency
      if (this.$refs.dependencyRef.validate() == false) {
        valid = false;
      }
      if ((this.title == undefined || this.title == "") && (this.subheading == undefined || this.subheading == "")) {
        this.textBoxTextError = true;
        valid = false;
      }
      if (valid == false) {
        return false;
      }
      this.toolStatus = true;
      return true;
    },
  },
};
</script>
