<template>
  <div>
    <span class="lang-flag"><country-flag :country="flagCode" size="small" /></span>
    <select v-model="$i18n.locale" @change="setFlag($event)">
      <option v-for="(lang, i) in langs" :key="`Lang${i}`" :value="lang.code">
        {{ lang.language }}
      </option>
    </select>
  </div>
</template>

<script>
import CountryFlag from "vue-country-flag-next";

export default {
  name: "locale-changer",
  components: { CountryFlag },
  data() {
    return {
      langs: [
        { code: "en", language: "English" },
        { code: "fr", language: "French" },
      ],
      flagCode: "gb",
    };
  },
  created() {
    this.setFlag();
  },
  methods: {
    setFlag() {
      if (this.$i18n.locale == "en") {
        this.flagCode = "gb";
      } else {
        this.flagCode = this.$i18n.locale;
      }
      this.$emit("languageChanged");
    },
  },
};
</script>

<style>
.lang-flag {
  margin-right: 10px;
}
</style>
