<template>
  <div v-show="showFields">
    <FormFields @closeFields="closeFields" />
  </div>
  <div class="form_section">
    <div class="form_section_heading">
      {{ $t("tools.target_field") }}
    </div>
    <div v-show="!showDetails" class="form_section_detail form_section_loading">{{ $t("general.loading") }}</div>
    <div v-show="showDetails" class="form_section_detail">
      <div class="flex_container">
        <div class="flex_item">
          <label :for="selectedField">{{ $t("target.field") }}</label>
          <b-form-select class="targetSelect" v-model="selectedField" :options="fields" @change="setTarget($event)" :class="{ 'input-error': targetSelectError }"></b-form-select>
        </div>
        <div v-show="selectedField != ''" class="flex_item">
          <div v-if="currentToolID == 5" class="required_checkbox">
            {{ $t("tools.required_boolean") }}
            <b-form-checkbox id="bs_override" v-model="requiredCheck" value="Y" unchecked-value="N" @change="setRequired($event)"> </b-form-checkbox>
          </div>
          <div v-else class="required_checkbox">
            {{ $t("tools.required") }}
            <b-form-checkbox id="bs_override" v-model="requiredCheck" value="Y" unchecked-value="N" @change="setRequired($event)"> </b-form-checkbox>
          </div>
        </div>
        <div v-show="selectedField == 'xxx'" class="flex_item flex_right">
          <b-button class="form_button" @click="clearField()">{{ $t("buttons.clear") }}</b-button>
        </div>
        <div class="flex_item flex_right">
          <b-button class="form_button" @click="addField()">{{ $t("buttons.fields") }}</b-button>
        </div>
      </div>
      <div class="flex_container">
        <div class="flex_item">
          <div v-if="formSource != ''">{{ $t("target.field_form_source") }} {{ formSource }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "./Tool.css";
import Axios from "axios";
import FormFields from "./FormFields.vue";

export default {
  name: "TargetField",
  emits: ["updateTarget", "closeFields"],
  components: { FormFields },
  props: ["target", "formItem", "formItems", "targetSelectError"],
  data() {
    return {
      fields: [],
      currentToolID: null,
      selectedField: "",
      requiredCheck: "",
      formSource: "",
      showFields: false,
      showDetails: false,
    };
  },
  created() {
    //Get the available target fields
    this.currentToolID = this.formItem.toolTypeID;
    const url = process.env.VUE_APP_CM_STORE + "/api/rest.php";
    const postdata = {
      module: "forms",
      action: "all_fields_by_type",
      details: { fieldTypeID: this.formItem.toolTypeID },
    };
    const headers = { "Content-Type": "application/json" };
    Axios.post(url, postdata, headers).then((res) => {
      var targetsInUse = [];
      this.formItems.forEach((formItem) => {
        formItem.columnDetails.forEach((columnDetail) => {
          // Get a copy of the tool being moved
          if (columnDetail.cellDetails.toolDetails && columnDetail.cellDetails.toolDetails.target) {
            targetsInUse.push(columnDetail.cellDetails.toolDetails.target.targetField);
          }
        });
      });

      var currentTarget = "";
      if (this.formItem.toolDetails.target) {
        currentTarget = this.formItem.toolDetails.target.targetField;
      }
      res.data.forEach((value) => {
        const fieldRow = { value: value.fieldName, text: value.fieldName, fieldType: value.fieldType, defaultValue: value.default_value, formSource: value.FieldSourceField };
        if (targetsInUse.indexOf(value.fieldName) != -1 && value.fieldName != currentTarget) {
          // element found
        } else {
          this.fields.push(fieldRow);
        }
      });
      if (this.formItem.toolDetails.target) {
        this.selectedField = this.formItem.toolDetails.target.targetField;
        this.getSource();
        if (this.formItem.toolDetails.target.required) {
          this.requiredCheck = this.formItem.toolDetails.target.required;
        }
      }
      this.showDetails = true;
    });
  },
  methods: {
    getSource() {
      const index = this.fields.find((rowIndex) => rowIndex.value == this.selectedField);
      this.formSource = index.formSource;
    },
    closeFields(formFields) {
      this.fields = [];
      // Gets the field type of this tool
      var fieldType = [];
      switch (this.formItem.toolTypeID) {
        case 4:
          fieldType.push("Date");
          fieldType.push("DateTime");
          fieldType.push("Time");
          fieldType.push("DateRange");
          fieldType.push("DateRange2");
          // fieldType.push("DateTimeRange");
          fieldType.push("TimeRange");
          break;
        case 5:
          fieldType.push("YesNo");
          break;
        default:
          fieldType.push("Text");
          break;
      }

      // Adds the fields to the drop down based on the current tool type (eg only shows date fields for a date tool)
      formFields.forEach((value) => {
        if (fieldType.includes(value.fieldType)) {
          const fieldRow = { value: value.fieldName, text: value.fieldName, fieldType: value.fieldType, formSource: value.formSource, defaultValue: value.defaultValue };
          this.fields.push(fieldRow);
        }
      });
      this.showFields = false;
    },
    clearField() {
      this.selectedField = "";
      this.requiredCheck = false;
      const target = { targetField: "", required: false, defaultValue: "", fieldType: "", formSource: "" };
      this.$emit("updateTarget", target);
    },
    addField() {
      this.showFields = true;
    },
    setTarget(chosenTargetField) {
      // CHECK - do I need to check of this target is already used?
      const index = this.fields.find((rowIndex) => rowIndex.value == chosenTargetField);
      this.formSource = index.formSource;
      const target = { targetField: chosenTargetField, required: this.requiredCheck, defaultValue: index.defaultValue, fieldType: index.fieldType, formSource: index.formSource };
      this.$emit("updateTarget", target);
    },
    setRequired(e) {
      const index = this.fields.find((rowIndex) => rowIndex.value == this.selectedField);
      const target = { targetField: this.selectedField, required: e, defaultValue: index.defaultValue, fieldType: index.fieldType, formSource: index.formSource };
      this.$emit("updateTarget", target);
    },
  },
};
</script>
<style>
.targetSelect {
  width: 250px;
}
</style>
