<template>
  <div v-if="showConfirmContinue">
    <ConfirmContinue :origin="origin" :confirmMessage="confirmMessage" :fieldInUseList="fieldInUseList" @closeContinue="closeContinue" />
  </div>
  <div class="inner_backdrop">
    <div class="inner_form_modal">
      <div class="form_header">{{ $t("table_heading.database_fields") }}</div>
      <div class="form_body">
        <div v-if="loading">{{ $t(loadingText) }}</div>
        <div v-if="!loading">
          <div class="field_table">
            <b-table striped hover :items="formFields" :fields="fields" responsive="sm" sort-icon-left>
              <template #head(fieldID)="data"> {{ $t(data.label) }}</template>
              <template #head(fieldName)="data"> {{ $t(data.label) }}</template>
              <template #head(fieldType)="data"> {{ $t(data.label) }}</template>
              <template #head(defaultValue)="data"> {{ $t(data.label) }}</template>
              <template #head(formSource)="data"> {{ $t(data.label) }}</template>
              <template #head(delete)="data"> {{ $t(data.label) }}</template>
              <template #cell(delete)="row">
                <b-button size="sm" @click="deleteField(row)" class="mr-2"> Delete </b-button>
              </template>
            </b-table>
          </div>

          <div class="new_field">
            <div class="new_field_label">Add new field {{ message }}</div>
            <div class="flex_container">
              <div class="flex_item">
                <b-form-input v-model="newFieldName" :placeholder="$t('place_holders.field_name')" :class="{ 'input-error': newFieldNameError }"></b-form-input>
              </div>
              <div class="flex_item">
                <b-form-select v-model="newFieldType" :options="fieldTypeOptions" :class="{ 'input-error': newFieldTypeError }"></b-form-select>
              </div>
              <div class="flex_item">
                <b-button class="form_button_sm" @click="addField()">Add</b-button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="form_footer">
        <b-button class="form_button" @click="closeElement()">Close</b-button>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import ConfirmContinue from "../ConfirmContinue.vue";

export default {
  name: "Settings",
  components: { ConfirmContinue },
  emits: ["closeFields"],
  data() {
    return {
      newFieldName: "",
      newFieldType: "",
      newFieldNameError: false,
      newFieldTypeError: false,
      message: "",
      fields: [
        { key: "fieldID", label: "table_heading.field_id", locale: "IDX", sortable: true },
        { key: "fieldName", label: "table_heading.field_name", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "fieldType", label: "table_heading.field_type", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "defaultValue", label: "table_heading.field_default_value", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "formSource", label: "table_heading.field_form_source", sortable: true, thClass: "alignleft", tdClass: "alignleft" },
        { key: "delete", label: "table_heading.field_delete", sortable: false },
      ],
      fieldTypeOptions: [
        { value: "", text: "Please select ..." },
        { value: "Text", text: "Text" },
        { value: "Date", text: "Date" },
        { value: "DateTime", text: "Date time" },
        { value: "Time", text: "Time" },
        { value: "DateRange", text: "Date range" },
        { value: "DateRange2", text: "Date range 2" },
        // { value: "DateTimeRange", text: "Date time range" },
        { value: "TimeRange", text: "Time range" },
        { value: "YesNo", text: "Yes/No" },
      ],

      formFields: [],
      fieldInUseList: [],
      fieldToAction: null,
      showConfirmContinue: false,
      loading: false,
      loadingText: "general.loading",
      url: process.env.VUE_APP_CM_STORE + "/api/rest.php",
      headers: { "Content-Type": "application/json" },
      postdata: {
        module: "forms",
        details: {},
      },
    };
  },
  created() {
    //Get the available target fields
    this.loading = true;
    this.postdata.action = "all_fields";
    Axios.post(this.url, this.postdata, this.headers).then((res) => {
      res.data.forEach((value) => {
        const fieldRow = { fieldID: value.fieldID, fieldName: value.fieldName, fieldType: value.fieldType, defaultValue: value.default_value, formSource: value.FieldSourceField };
        this.formFields.push(fieldRow);
      });
      this.loading = false;
    });
  },
  methods: {
    addField() {
      if (this.validate()) {
        this.postdata.action = "add_field";
        this.postdata.details = { fieldName: this.newFieldName, fieldType: this.newFieldType };

        Axios.post(this.url, this.postdata, this.headers).then((res) => {
          this.newFieldName = "";
          this.newFieldType = "";
          if (res.data == "field_name_exists") {
            this.message = "Field name aready exists";
          } else {
            const fieldID = res.data;
            this.postdata.details.fieldID = fieldID;
            this.formFields.push(this.postdata.details);
          }
        });
      }
    },

    deleteField(formRow) {
      this.fieldToAction = formRow.item.fieldID;
      this.origin = "loadFields";
      this.confirmMessage = "Are you sure you want to delete this field?";
      this.confirmType = "delete";
      this.showConfirmContinue = true;
    },

    deleteFieldConfirmed() {
      this.postdata.action = "delete_field";
      this.postdata.details = { fieldID: this.fieldToAction };
      Axios.post(this.url, this.postdata, this.headers).then((res) => {
        if (res.data == true) {
          // field deleted
          const index = this.formFields.find((rowIndex) => rowIndex.fieldID == this.fieldToAction);
          this.formFields.splice(this.formFields.indexOf(index), 1);
        } else {
          this.showConfirmContinue = true;
          this.origin = "fieldsInUse";
          this.confirmMessage = "It is not possible to delete this field as it is being used in the following forms: ";
          this.fieldInUseList = [];
          res.data.forEach((value) => {
            var item = { form: value };
            this.fieldInUseList.push(item);
          });
        }
      });
    },
    closeContinue(e) {
      this.showConfirmContinue = false;
      if (e == "Y") {
        this.deleteFieldConfirmed();
      }
      this.confirmMessage = null;
    },
    closeElement() {
      const formFields = this.formFields;
      this.$emit("closeFields", formFields);
    },
    validate() {
      this.message = "";
      this.newFieldNameError = false;
      this.newFieldTypeError = false;

      var valid = true;
      if (this.newFieldName == "") {
        this.newFieldNameError = true;
        valid = false;
      }
      if (this.newFieldType == "") {
        this.newFieldTypeError = true;
        valid = false;
      }

      //not-acceptable char
      const pattern = /^([a-zA-Z0-9 _-]+)$/;
      if (pattern.test(this.newFieldName)) {
        this.message = "";
      } else {
        valid = false;
        this.message = " - invalid format";
        this.newFieldNameError = true;
      }

      return valid;
    },
  },
};
</script>

<style scoped>
.inner_backdrop {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 800;
  background: rgba(0, 0, 0, 0.2);
}
.inner_form_modal {
  position: absolute;
  top: 2%;
  bottom: 15%;
  left: 10%;
  right: 10%;
  margin: auto;
}
.new_field_label {
  padding-left: 15px;
}
.new_field {
  padding: 20px;
  text-align: left;
  margin-top: 40px;
}
.flex_container {
  display: flex;
}
.flex_item {
  flex: auto;
  padding: 5px;
}
.field_table {
}
</style>
