<template>
  <div class="backdrop">
    <div class="form_modal">
      <div class="form_header">
        {{ $t("tools.settings") }}
      </div>
      <div class="form_body">
        <!-- <div class="form_section">
          <div class="form_section_heading">{{ $t("settings.label_position") }}</div>
          <div class="form_section_detail">
            <div>Label to the left of element, or above:</div>
            <b-form-select v-model="labelPosition" :options="labelOptions"></b-form-select>
          </div>
        </div> -->

        <div class="form_section">
          <div class="form_section_heading">{{ $t("settings.date_format") }}</div>
          <div class="form_section_detail flex_container">
            <div class="flex_item">
              <div>How dates will be displayed:</div>
              <b-form-select v-model="dateFormat">
                <b-form-select-option-group label="Hyphen separator" :options="dateFormatHyphenOptions"> </b-form-select-option-group>
                <b-form-select-option-group label="Dot separator" :options="dateFormatDotOptions"> </b-form-select-option-group>
                <b-form-select-option-group label="Slash separator" :options="dateFormatSlashOptions"> </b-form-select-option-group>
                <b-form-select-option-group label="Other" :options="dateFormatOtherOptions"> </b-form-select-option-group>
              </b-form-select>
            </div>

            <div class="flex_item">
              <div>How times will be displayed:</div>
              <b-form-select v-model="timeFormat" :options="timeFormatOptions"> </b-form-select>
            </div>
            <div class="flex_item">
              <div>Date picker theme:</div>
              <b-form-select v-model="themeFormat" :options="dateThemeOptions"> </b-form-select>
            </div>
          </div>
        </div>

        <div class="form_section">
          <div class="form_section_heading">{{ $t("settings.section_settings") }}</div>
          <div class="form_section_detail flex_container">
            <div class="flex_item">
              <div>{{ $t("settings.column_gap") }}</div>
              <b-form-input v-model="columnGap" type="number" min="0" max="50" step="5"></b-form-input>
            </div>
            <div class="flex_item">
              <div>{{ $t("settings.row_gap") }}</div>
              <b-form-input v-model="rowGap" type="number" min="0" max="50" step="5"></b-form-input>
            </div>
          </div>
          <div class="form_section_detail flex_container">
            <div class="flex_item">
              <div>{{ $t("settings.background_colour") }}</div>
              <input type="color" v-model="sectionBackColour" />
            </div>
            <div class="flex_item">
              <div>{{ $t("settings.border_size") }}</div>
              <input type="number" class="number_centre" min="0" max="10" v-model="sectionBorderSize" />
            </div>
            <div class="flex_item">
              <div>{{ $t("settings.border_colour") }}</div>
              <input type="color" v-model="sectionBorderColour" />
            </div>
            <div class="flex_item">
              <div>{{ $t("settings.border_radius") }}</div>
              <input type="number" class="number_centre" min="0" max="10" v-model="sectionBorderRadius" />
            </div>
          </div>
        </div>

        <div class="form_section">
          <div class="form_section_heading">{{ $t("settings.element_settings") }}</div>
          <div class="form_section_detail flex_container">
            <div class="flex_item">
              <div>{{ $t("settings.text_colour") }}</div>
              <input type="color" v-model="elementTextColour" />
            </div>
          </div>
          <div class="form_section_detail flex_container">
            <div class="flex_item">
              <div>{{ $t("settings.background_colour") }}</div>
              <input type="color" v-model="elementBackColour" />
            </div>

            <div class="flex_item">
              <div>{{ $t("settings.border_size") }}</div>
              <input type="number" class="number_centre" min="0" max="10" v-model="elementBorderSize" />
            </div>
            <div class="flex_item">
              <div>{{ $t("settings.border_colour") }}</div>
              <input type="color" v-model="elementBorderColour" />
            </div>
            <div class="flex_item">
              <div>{{ $t("settings.border_radius") }}</div>
              <input type="number" class="number_centre" min="0" max="10" v-model="elementBorderRadius" />
            </div>
          </div>
        </div>
      </div>

      <div class="form_footer">
        <b-button class="form_button" @click="setSectionDefault()">{{ $t("general.default") }}</b-button>
        <b-button class="form_button" @click="closeElement()">{{ $t("buttons.cancel") }}</b-button>
        <b-button class="form_button" @click="saveElement()">{{ $t("buttons.save") }}</b-button>
      </div>
    </div>
  </div>
</template>
<script>
import "./Tool.css";

export default {
  name: "Setting",
  components: {},
  props: ["formSettings", "closeTool"],
  data() {
    return {
      labelOptions: [
        { value: "above", text: "Above" },
        { value: "left", text: "Left" },
      ],
      dateFormatDotOptions: [
        { value: "d.m.y", text: "19.08.22" },
        { value: "m.d.y", text: "08.19.22" },
        { value: "y.m.d", text: "22.19.08" },
        { value: "d.m.Y", text: "19.08.2022" },
        { value: "m.d.Y", text: "08.19.2022" },
        { value: "Y.m.d", text: "2022.08.19" },
      ],
      dateFormatHyphenOptions: [
        { value: "d-m-y", text: "19-08-22" },
        { value: "m-d-y", text: "08-19-22" },
        { value: "y-m-d", text: "22-19-08" },
        { value: "d-m-Y", text: "19-08-2022" },
        { value: "m-d-Y", text: "08-19-2022" },
        { value: "Y-m-d", text: "2022-08-19" },
      ],
      dateFormatSlashOptions: [
        { value: "d/m/y", text: "19/08/22" },
        { value: "m/d/y", text: "08/19/22" },
        { value: "y/m/d", text: "22/19/08" },
        { value: "d/m/Y", text: "19/08/2022" },
        { value: "m/d/Y", text: "08/19/2022" },
        { value: "Y/m/d", text: "2022/08/19" },
      ],
      dateFormatOtherOptions: [
        { value: "F j, Y", text: "August 19, 2022" },
        { value: "j F Y", text: "19 August 2022" },
      ],
      timeFormatOptions: [
        { value: "24hr", text: "24 hour" },
        { value: "h:i K", text: "AM / PM" },
      ],
      dateThemeOptions: [
        { value: "dark", text: "Dark" },
        { value: "material_blue", text: "Material blue" },
        { value: "material_green", text: "Material green" },
        { value: "material_red", text: "Material red" },
        { value: "material_orange", text: "Material orange" },
        { value: "confetti", text: "Confetti" },
      ],
      headingText: "",
      curentFormSettings: {},
      labelPosition: "above",
      dateFormat: "Y-m-d",
      timeFormat: "24hr",
      columnGap: "10",
      rowGap: "0",
      sectionBackColour: "#ffffff",
      sectionTextColour: "#000000",
      sectionBorderColour: "#ffffff",
      sectionBorderSize: "0",
      sectionBorderRadius: "0",
      elementBackColour: "#ffffff",
      elementTextColour: "#000000",
      elementBorderSize: "0",
      elementBorderColour: "#ffffff",
      elementBorderRadius: "0",
      themeFormat: "material_blue",
    };
  },
  created() {
    //Get the exisitng form settings
    if (this.formSettings) {
      this.curentFormSettings = this.formSettings;
      this.labelPosition = this.curentFormSettings.labelPosition;

      this.dateFormat = this.curentFormSettings.dateFormat;
      this.timeFormat = this.curentFormSettings.timeFormat;
      this.themeFormat = this.curentFormSettings.themeFormat;
      this.columnGap = this.curentFormSettings.columnGap;
      if (this.curentFormSettings.rowGap) {
        this.rowGap = this.curentFormSettings.rowGap;
      }
      // Section settings
      this.sectionBackColour = this.curentFormSettings.sectionBackColour;
      this.sectionTextColour = this.curentFormSettings.sectionTextColour;
      this.sectionBorderColour = this.curentFormSettings.sectionBorderColour;
      this.sectionBorderSize = this.curentFormSettings.sectionBorderSize;
      this.sectionBorderRadius = this.curentFormSettings.sectionBorderRadius;

      // Element settings
      this.elementBackColour = this.curentFormSettings.elementBackColour;
      this.elementTextColour = this.curentFormSettings.elementTextColour;
      this.elementBorderColour = this.curentFormSettings.elementBorderColour;
      this.elementBorderSize = this.curentFormSettings.elementBorderSize;
      this.elementBorderRadius = this.curentFormSettings.elementBorderRadius;
    }
    //quick fix
    this.columnGap = this.columnGap.replace("px", "");
  },
  methods: {
    setSectionDefault() {
      this.labelPosition = "above";
      this.dateFormat = "Y-m-d";
      this.timeFormat = "24hr";
      this.columnGap = "10";
      this.rowGap = "0";
      this.sectionBackColour = "#ffffff";
      this.sectionTextColour = "#000000";
      this.sectionBorderColour = "#ffffff";
      this.sectionBorderSize = "0";
      this.sectionBorderRadius = "0";
      this.elementBackColour = "#ffffff";
      this.elementTextColour = "#000000";
      this.elementBorderColour = "#ffffff";
      this.elementBorderSize = "0";
      this.elementBorderRadius = "0";
      this.themeFormat = "material_blue";
    },
    closeElement() {
      this.$emit("closeTool", this.formSettings);
    },
    saveElement() {
      const newSettings = {
        labelPosition: this.labelPosition,
        dateFormat: this.dateFormat,
        timeFormat: this.timeFormat,
        themeFormat: this.themeFormat,
        columnGap: this.columnGap,
        rowGap: this.rowGap,
        sectionBackColour: this.sectionBackColour,
        sectionTextColour: this.sectionTextColour,
        sectionBorderColour: this.sectionBorderColour,
        sectionBorderSize: this.sectionBorderSize,
        sectionBorderRadius: this.sectionBorderRadius,
        elementBackColour: this.elementBackColour,
        elementTextColour: this.elementTextColour,
        elementBorderColour: this.elementBorderColour,
        elementBorderSize: this.elementBorderSize,
        elementBorderRadius: this.elementBorderRadius,
      };
      this.curentFormSettings = newSettings;
      this.$emit("saveSettings", this.curentFormSettings);
    },
  },
};
</script>
